<template>
  <div class="combine" @dragover="dragover" @dragstart="dragstart" @drop="drop" v-show="showCombine" id="combine">
      <div class="customize-win-header">
           <div @click="closeCombine" class="close iconfont icon-guanbi"></div>
      </div>
      <div class="row" style="font-size:20px;font-weight:bold;">新建组合</div>
      <div class="row" ><span>从左侧将设备拖入生成组合</span> <span><Button @click="deleteAll()">清除</Button></span></div>
      <div class="row">
          <span style="color:red">*</span><span>名称：</span><input style="width:100px"  class="safe-input" placeholder="名字/编号" type="text"  v-model="combine.name"/>
          <span>
              <span>对齐方式：</span>
            <RadioGroup v-model="alignType" @on-change="updateAlign">
                <Radio label="0">背面</Radio>
                <Radio label="1">操作面</Radio>
            </RadioGroup>
         </span> 
    </div>
    <div class="row cavans" id="combineCavans">
        <div id="combineEditor"></div>
    </div>
    <div class="row">
        <span style="padding-right : 20px">安全距离：</span>
        <span><span>上侧：</span><input  class="safe-input" placeholder="单位cm，默认为0" type="text"  v-model="combine.safe.up"/></span>
        <span><span>下侧：</span><input  class="safe-input" placeholder="单位cm，默认为0" type="text"  v-model="combine.safe.down"/></span>
        <span><span>左侧：</span><input  class="safe-input" placeholder="单位cm，默认为0" type="text"  v-model="combine.safe.left"/></span>
        <span><span>右侧：</span><input  class="safe-input" placeholder="单位cm，默认为0" type="text"  v-model="combine.safe.right"/></span> 
    </div>

    <div class="row">
        <span style="padding-right : 20px">额外距离：</span>
        <span><span>操作面与操作面：</span><input  class="safe-input" placeholder="单位cm，默认为0" type="text"  v-model="combine.safe.operation"/></span>
        <span><span>背面与背面：</span><input  class="safe-input" placeholder="单位cm，默认为0" type="text"  v-model="combine.safe.back"/></span> 
        <Button  style="float:right; margin-right:30px" @click="addCombine" type="primary">确定</Button> 
    </div>
  </div>
</template>

<script>
import combineEditor from '../flow/combineEditor';
import eventBus from '../eventbus';

export default {
  name: 'Combine',

  data(){
     return{
        showCombine : false,
        combine : {
            name : '',
            safe : {
                up : 0,
                right : 0,
                down : 0,
                left : 0,
                operation: 0, 
                back: 0
            }
        },
        alignType: "0"
     }
  },
  mounted(){
    var that=this;
    eventBus.$on('showCombine',(e)=>{
       that.showCombine = e.isShow;
       that.editor=new combineEditor('combineEditor');
     });
  },
  methods:{
     dragover(e){
       e.preventDefault();
     },
     dragstart(e){
       e.preventDefault();
     },
     drop(e){
        this.editor.alignType = this.alignType;
        var text= e.dataTransfer.getData("addShape");
        var x,y;
        if(e.offsetX) {
          x=parseInt(e.offsetX)+0.5;
          y=parseInt(e.offsetY)+0.5;
        } else {
          x=parseInt(e.pageX)+0.5 - 140;
          y=parseInt(e.pageY)+0.5 - 50;
        }

        try{
          var data=JSON.parse(text);
          data['x']=x;
          data['y']=y;
          data.alignType = this.alignType;
          this.editor.addShape(data);
        }catch(err){
           //window.console.log(err);
        }
     },
     trim(x) {
        return x.replace(/^\s+|\s+$/gm,'');
     },
      deleteAll () {
        this.editor.execute('deleteAll',{node:this.node});
     },
     addCombine() {
       var nodes = this.editor.nodes;
       if(this.combine.name !== '' && nodes.length > 0) {
         var config = {
           safe: this.combine.safe,
           name: this.combine.name,
           alignType : this.alignType,
           face : this.alignType === '0' ? '2' : '0'
         }
        var node = this.buildCombineNode(nodes, config)
        eventBus.$emit('addCombine', {node : node});
        this.closeCombine();
       }
     },
     closeCombine () {
       this.showCombine=false;
       eventBus.$emit('closeCombine');
     },
     updateAlign (val) {
         this.editor.updateAlign(val); 
     }
  }
}
</script>

<style scoped>
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.row {
    margin-bottom:10px;
    margin-left:20px
}
.combine{
    overflow-y: scroll;
    width:680px;
    height:650px;
    border-radius:3px;
    position: fixed;
    left:66%;
    top:65%;
    transform: translate(-50%,-50%);
    z-index: 150;
    border:1px solid #f5f5f5;
    background: #fff;
}

.customize-win-header{
  height:30px;
  line-height: 30px;
  text-indent: 6px;
  font-size: 12px;
  background: #f9f9f9;
  margin-bottom:10px;
}
.cavans {
    width: 625px;
    height: 250px;
    border-style:dotted;
}
.close{
  position: absolute;
  right:0;
  top:0;
  width:30px;
  height:30px;
  color:red;
  text-align: center;
  line-height: 30px;
  cursor: pointer;
  font-size: 18px;
}

.image-url{
  width:400px;
  height:30px;
  line-height: 30px;
  border-radius:2px;
  font-size: 12px;
  border:1px solid #ccc;
  margin-left:6px;
  text-indent: 6px;
  margin-right:6px;
}

.safe-input{
  width:50px;
  height:30px;
  line-height: 30px;
  border-radius:2px;
  font-size: 12px;
  border:1px solid #ccc;
  margin-left:6px;
  text-indent: 6px;
  margin-right:6px;
}


.foot-button{
  margin-left:200px
}
</style>
