import zrender from 'zrender';
import mixin from '../help/mixin'
import common from './common'
class Text extends zrender.Text {
    constructor(data) {
        super(data);
        this.data = data;
        this.oldfill = this.data.style.fill;
        this.anchors = [];
        this.nodeType = "node";
    }
}

mixin(common,Text.prototype);

export default Text;