export default {
    install(Vue) {
        Vue.prototype.buildCombineNode = function (nodes, config) {
            var w =0, h = 0;
            for(let i=0; i<nodes.length; i++) {
                w += nodes[i].data.originData.width;
                h = Math.max(h, nodes[i].data.originData.height)
              }
              var calCost = function(nodes) {
                  let cost = 0;
                  for(let i=0; i<nodes.length; i++) {
                      cost += nodes[i].cost;
                  }
                  return cost;
              };
              var childNodes = [];
              nodes.forEach(node => {
                childNodes.push({
                  name : node.name,
                  data : node.data,
                  safe : node.safe,
                  alignFace : node.safe,
                  kind : node.kind,
                  face : node.face,
                  style: node.style,
                  text: node.text,
                  cost: node.cost
                })
              });
              return {
                width : w,
                height: h,
                safe : config.safe,
                kind : 5,
                name : config.name,
                nodes: childNodes,
                alignType : config.alignType,
                alignFace : "0",
                face : config.alignType === '0' ? '2' : '0',
                cost: calCost(nodes)
              }
        }

        Vue.prototype.combineCommandHelper = function(item, scale){
            var w = Number.parseFloat(item.width)/scale,
                h = Number.parseFloat(item.height)/scale,
                face = item.face,
                alignFace = item.alignFace;
      
            var buildNodes = function(nodes) {
              var res = []
              for(let i=0; i<nodes.length; i++) {
                res.push({
                  width: nodes[i].data.originData.width/scale,
                  height: nodes[i].data.originData.height/scale,
                  name : nodes[i].data.style.text
                })
              }
              return res;
            };
            var safe = {
                up : Number.parseFloat(item.safe.up),
                down : Number.parseFloat(item.safe.down),
                left : Number.parseFloat(item.safe.left),
                right : Number.parseFloat(item.safe.right),
                operation : Number.parseFloat(item.safe.operation),
                back : Number.parseFloat(item.safe.back)
            };
            return {
              style: {
                  fill: '#fff',
                  stroke: '#333',
                  lineWidth: 1,
                  text: '',
                  textFill: '#333',
                  fontWeight: 'normal',
                  fontStyle: '',
                  textPosition: 'inside',
                  fontSize: 12,
                  opacity:1
              },
              safe: safe,
              face: face,
              alignFace: alignFace,
              kind : 3, //按照整体一个设备处理
              shape: {
                  width: w,
                  height: h,
                  nodes: buildNodes(item.nodes),
              },  
              z: 200,
              draggable: true,
              originData : {
                name : item.name,
                nodes : item.nodes,
                isCombine : true,
                width: item.width,
                height: item.height,
                cost: item.cost,
                safe : safe,
                extrSafe: item.extrSafe
              },
              command: "combine"
            }
          }
      
    }
}
