import zrender from 'zrender';
import mixin from '../help/mixin'
import common from './common'
class Rect extends zrender.Rect {
    constructor(data) {
        super(data);
        this.data = data;
        this.oldfill = this.data.style.fill;
        this.anchors = [];
        this.nodeType = "node";
        this.createAnchors();
    }
   
    createAnchors() {
    }
  
}
mixin(common,Rect.prototype);

export default Rect;