<template>
  <div class="toolbar">
    <Collapse v-model="currentPanel" accordion>
          <Panel name="1">
            添加房间
          <div slot="content" class="obpanel">
            <!-- <List class="clearfix child-panel">
             <ListItem style= "text-align:center;" draggable="true" v-for="(item,index) in precustomize" v-bind:key="index"  @dragstart="dragstart($event,item)">
               <div draggable="true"  @dragstart="dragstart($event,item)" style="font-size:16px;overflow: hidden; text-overflow:ellipsis; white-space: nowrap; text-align:center; width:100%" v-text="item.roomName"></div>
             </ListItem>
            </List> -->
              <List  class="clearfix child-panel">
              <ListItem v-for="(item,index) in precustomize" v-bind:key="index" >
                <div style="margin:20px 0;box-shadow: 0px 2px 5px 0px rgba(81, 85, 90, 0.08);
    border-radius:4px;">
                  <table style="background-color:white;" draggable="true"  @dragstart="dragstart($event,item)">
                    <tr>
                        <td>
                          <Avatar :style="{background: 'rgba(0,0,0,0)', color:'#0D6EFD', border : '1px solid #0D6EFD'}">房间</Avatar>
                        </td>
                        <td >
                          <tr>
                            <div style="margin-left:10px">{{item.id}}</div>
                          </tr>
                          <tr>
                            <div style="width:90px;overflow: hidden; text-overflow:ellipsis; white-space: nowrap;">{{item.roomName}}</div>
                          </tr>
                        </td>
                    </tr>
                  </table>
                </div>
              </ListItem>
            </List >
             <div class="panelFootButton">
              <Button style = "margin-right:15px" @click.stop="showUpload=!showUpload">
                添加
              </Button>
              <Button @click.stop="showCustomize=!showCustomize">
                管理
              </Button>
             </div>
          </div>
        </Panel>
        <Panel name="2" class="facilitypanel">
            设备
          <div slot="content">
            <List  class="clearfix child-panel" style= "left:0px;text-align:center;">
              <ListItem style= "margin-left:20px">
                <Button :type="button1Statu" style="width:200px;" @click="updateFacilityDialog(0)">
                   高压
                  <Icon type="ios-arrow-forward"></Icon>
                </Button>
              </ListItem>
              <ListItem style= "margin-left:20px">
                <Button :type="button2Statu" style="width:200px" @click="updateFacilityDialog(1)">
                   低压
                  <Icon type="ios-arrow-forward"></Icon>
                </Button>
              </ListItem>
              <ListItem style= "margin-left:20px">
                <Button :type="button3Statu" style="width:200px" @click="updateFacilityDialog(2)">
                   其他
                  <Icon type="ios-arrow-forward"></Icon>
                </Button>
              </ListItem>
            </List >
            <div class="panelFootButton">
              <Button style = "padding:0 8px" @click.stop="showSearchDialog=!showSearchDialog">
                搜索
              </Button>
              <Button style = "padding:0 8px; margin:0 2px" @click="addFacilityDetail()">
                添加
              </Button>
              <Button style = "padding:0 8px" @click.stop="showFacilityManageDialog=!showFacilityManageDialog">
                管理
              </Button>
            </div>
          </div>
        </Panel>
        <Panel name="3">
            组合
          <div slot="content" class="obpanel">
            <!-- <List class="clearfix child-panel">
              <ListItem draggable="true"  @dragstart="dragstart($event,item)" v-for="item in combines" v-bind:key="item.command" :class="'iconfont '+item.name">
                <div draggable="true"  @dragstart="dragstart($event,item)" v-html="item.path" :title="item.text"></div>
              </ListItem>
            </List> -->
            <List  class="clearfix child-panel">
              <ListItem v-for="(item,index) in combines" v-bind:key="index" >
                <div style="margin:20px 0;box-shadow: 0px 2px 5px 0px rgba(81, 85, 90, 0.08);
    border-radius:4px;">
                  <table style="background-color:white;" draggable="true"  @dragstart="dragstart($event,item)">
                    <tr>
                        <td>
                          <Avatar :style="{background: 'rgba(0,0,0,0)', color:'#0D6EFD', border : '1px solid #0D6EFD'}">{{item.name}}</Avatar>
                        </td>
                        <td >
                          <tr>
                            <div style="margin-left:10px">{{item.id}}</div>
                          </tr>
                          <tr>
                            <div style="width:90px;overflow: hidden; text-overflow:ellipsis; white-space: nowrap;">{{buildParameter(item)}}</div>
                          </tr>
                        </td>
                    </tr>
                  </table>
                </div>
              </ListItem>
            </List >
             <div class="panelFootButton">
              <Button style = "margin-right:15px" @click.stop="showCombinePanel">
                添加
              </Button>
              <Button @click.stop="showCombineManageDialog=!showCombineManageDialog">
                管理
              </Button>
             </div>
          </div>
        </Panel>
        <Panel name="4">
            墙/柱子
          <div slot="content" class="obpanel">
            <List  class="clearfix child-panel">
              <ListItem v-for="(item,index) in preObstacle" v-bind:key="index" >
                <div style="margin:20px 0;box-shadow: 0px 2px 5px 0px rgba(81, 85, 90, 0.08);
    border-radius:4px;">
                  <table style="background-color:white;" draggable="true"  @dragstart="dragstart($event,item)">
                    <tr>
                        <td>
                          <Avatar :style="{background: 'rgba(0,0,0,0)', color:'#0D6EFD', border : '1px solid #0D6EFD'}">{{item.name}}</Avatar>
                        </td>
                        <td >
                          <tr>
                            <div style="margin-left:10px">{{item.id}}</div>
                          </tr>
                          <tr>
                            <div style="width:90px;overflow: hidden; text-overflow:ellipsis; white-space: nowrap;">{{buildParameter(item)}}</div>
                          </tr>
                        </td>
                    </tr>
                  </table>
                </div>
              </ListItem>
            </List >
             <div class="panelFootButton">
              <Button style = "margin-right:15px" @click.stop="showObstacleDetail=!showObstacleDetail">
                添加
              </Button>
              <Button @click.stop="showObstacleDialog=!showObstacleDialog" >
                管理
              </Button>
             </div>
          </div>
        </Panel>
    </Collapse>

      <!-- <Drawer title="Basic Drawer" :closable="false" v-model="showSearchDialog">
          <p>Some contents...</p>
          <p>Some contents...</p>
          <p>Some contents...</p>
      </Drawer> -->

     <div class="search-win" v-if="showSearchDialog">
      <div class="customize-win-header">
           <div @click.stop="showSearchDialog=false" class="close iconfont icon-guanbi"></div>
      </div>
      <div class="upload-win-content">
          <div style="margin-bottom:20px; font-size:22px; font-weight:bold"><span>设备搜索</span></div>
          <div style="margin-bottom:20px"><input  class="image-url" placeholder="输入设备名称或者编号关键字搜索" type="text" text="serchKeyWord" v-model="serchKeyWord"/> <Button @click="clearSearch">重置</Button></div>
      </div>
    </div>

    <!-- 设备管理界面 -->
    <div class="customize-win"  v-if="showFacilityManageDialog">
        <div class="customize-win-header">
           <span style="font-weight:bold">设备管理</span>
           <div @click.stop="showFacilityManageDialog=false" class="close iconfont icon-guanbi"></div>
        </div>

        <Tabs value="name1">
        <TabPane label="高压" name="name1">
          <div class="customize-image" style="width:100%">
              <ul>
              <li draggable="true" v-for="(item,index) in facilityGroup0" v-bind:key="index">
                <span @click.stop="removeFacility(item)" class="iconfont icon-guanbi"></span>
                <div style="margin-top:20px;font-size:12px;max-width:100px;overflow: hidden; text-overflow:ellipsis; white-space: nowrap;" v-html="item.name"></div>
              </li>
            </ul>
          </div>
        </TabPane>
        <TabPane label="低压" name="name2">
          <div class="customize-image" style="width:100%">
              <ul>
              <li draggable="true" v-for="(item,index) in facilityGroup1" v-bind:key="index">
                <span @click.stop="removeFacility(item)" class="iconfont icon-guanbi"></span>
                <div style="margin-top:20px;font-size:12px;max-width:100px;overflow: hidden; text-overflow:ellipsis; white-space: nowrap;" v-html="item.name"></div>
              </li>
            </ul>
          </div>
        </TabPane>
        <TabPane label="其他" name="name3">
            <div class="customize-image" style="width:100%">
              <ul>
              <li draggable="true" v-for="(item,index) in facilityGroup2" v-bind:key="index">
                <span @click.stop="removeFacility(item)" class="iconfont icon-guanbi"></span>
                <div style="margin-top:20px;font-size:12px;max-width:100px;overflow: hidden; text-overflow:ellipsis; white-space: nowrap;" v-html="item.name"></div>
              </li>
            </ul>
            </div>
        </TabPane>
    </Tabs>

