        <Col span="8">
            <div class="customize-image" style="width:100%">
              <div>高压</div>
              <ul>
              <li draggable="true" v-for="(item,index) in facilityGroup0" v-bind:key="index">
                <span @click.stop="removeFacility(item)" class="iconfont icon-guanbi"></span>
