  </div>
    <Drawer title="请拖入画布" placement="left"  :mask="false" width="180" v-model="showFacilityDialog">
        <List  class="clearfix" style="width:160px;">
          <ListItem v-for="(item,index) in preFacilities" v-bind:key="index" >
            <div style="margin:20px 0;box-shadow: 0px 2px 5px 0px rgba(81, 85, 90, 0.08);
border-radius: 4px;">
              <table style="background-color:white;" draggable="true"  @dragstart="dragstart($event,item)"  @click="showFacilityMsg(item)">
                <tr>
                    <td>
                       <Avatar :style="{background: 'rgba(0,0,0,0)', color:'#0D6EFD', border : '1px solid #0D6EFD'}">{{item.name}}</Avatar>
                    </td>
                    <td >
                      <tr>
                        <div style="margin-left:10px">{{item.id}}</div>
                      </tr>
                      <tr>
                        <div style="color:#999999;margin-left:10px">{{buildParameter(item)}}</div>
                      </tr>
                    </td>
                </tr>
              </table>
            </div>
          </ListItem>
        </List >
        <!-- <div class="upload-win-footer">
          <button @click="addFacility">添加到画布</button>
        </div> -->
    </Drawer>


    <Drawer title="设备参数" mask-style = "background-color:rgba(0,0,0,0)" width="210" v-model="facilityDetail.isShow" >
      <div>
          <div style="margin-bottom:10px"><span style="color:red">*</span><span>名称：</span><input  v-bind:disabled="facilityDetail.mode!=0" class="draw-input" placeholder="设备名称" type="text"  v-model="facility.name"/></div>
          <div style="margin-bottom:10px"><span style="color:red">*</span><span>编号：</span><input  v-bind:disabled="facilityDetail.mode!=0" class="draw-input" placeholder="设备编号" type="text"  v-model="facility.id"/></div>
          <div style="margin-bottom:10px"><span style="color:red">*</span><span>宽度：</span><input  v-bind:disabled="facilityDetail.mode!=0" class="draw-input" placeholder="单位cm" type="text"  v-model="facility.width"/></div>
          <div style="margin-bottom:10px"><span style="color:red">*</span><span>高度：</span><input  v-bind:disabled="facilityDetail.mode!=0" class="draw-input" placeholder="单位cm" type="text"  v-model="facility.height"/></div>
          <div style="margin-bottom:10px"><span style="color:red">*</span><span>价格：</span><input  v-bind:disabled="facilityDetail.mode!=0" class="draw-input" placeholder="单位:元" type="text"  v-model="facility.cost"/></div>
          <div style="margin-bottom:10px">
              <input v-bind:disabled="facilityDetail.mode!=0" type="radio" name="group" v-model="facility.group" value="0" /><label for="高压" style="margin-right: 10px">高压</label>
              <input v-bind:disabled="facilityDetail.mode!=0" type="radio" name="group" v-model="facility.group" value="1" /><label for="低压" style="margin-right: 10px">低压</label>
              <input v-bind:disabled="facilityDetail.mode!=0" type="radio" name="group" v-model="facility.group" value="2" /><label for="其他" style="margin-right: 10px">其他</label>
          </div>
          <div style="margin-bottom:10px"><span style="font-weight:bold">安全距离</span></div>
          <div style="margin-bottom:10px"><span>上侧：</span><input  class="draw-input" placeholder="单位cm, 默认为0" type="text"  v-model="facility.safe.up"/></div>
          <div style="margin-bottom:10px"><span>下侧：</span><input  class="draw-input" placeholder="默认为0, 默认为0" type="text"  v-model="facility.safe.down"/></div>
          <div style="margin-bottom:10px"><span>左侧：</span><input  class="draw-input" placeholder="默认为0, 默认为0" type="text"  v-model="facility.safe.left"/></div>
          <div style="margin-bottom:10px"><span>右侧：</span><input  class="draw-input" placeholder="默认为0, 默认为0" type="text"  v-model="facility.safe.right"/></div>  
          <!-- <div style="margin-bottom:10px"><span style="font-weight:bold">额外安全距离</span></div>
          <div style="margin-bottom:10px"><span>操作面：</span><input style = "width:95px" class="draw-input" placeholder="单位cm，默认0" type="text"  v-model="facility.safe.operation"/></div>
          <div style="margin-bottom:10px"><span>背面：</span><input  class="draw-input" placeholder="单位cm，默认0" type="text"  v-model="facility.safe.back"/></div>   -->
          <div style="margin-bottom:20px" v-show="facilityDetail.mode == 0">
              <div style="margin-bottom:10px; font-weight:bold">操作台朝向</div>
              <input v-bind:disabled="facilityDetail.mode!=0" type="radio" name="face" v-model="facility.face" value="0" /><label for="up" style="margin-right: 15px">上</label>
              <input v-bind:disabled="facilityDetail.mode!=0" type="radio" name="face" v-model="facility.face" value="2" /><label for="down" style="margin-right: 15px">下</label>
              <input v-bind:disabled="facilityDetail.mode!=0" type="radio" name="face" v-model="facility.face" value="3" /><label for="left" style="margin-right: 15px">左</label>
              <input v-bind:disabled="facilityDetail.mode!=0" type="radio" name="face" v-model="facility.face" value="1" /><label for="right">右</label>
         </div>
        <div style='text-align:center'>
            <button @click="faciliyOkBtn" style='text-align:center; width:100px'>确定</button>
        </div>
      </div>
    </Drawer>

    <Drawer title="组合" mask-style = "background-color:rgba(0,0,0,0)" width="210" v-model="combineDetail.isShow">
      <div>
          <div style="margin-bottom:10px"><span>名称：</span><input disabled class="draw-input" placeholder="名字/编号" type="text"  v-model="combine.name"/></div>
          <div style="margin-bottom:10px">
          <span style="font-weight:bold">组成：</span>
          <List style="margin-left:40px;">
              <ListItem  v-for="item in combine.nodes" v-bind:key="item.name">
                <div v-html="item.name" :title="item.text"></div>
              </ListItem>
          </List >
          </div>
          <div style="margin-bottom:10px"><span>宽度：</span><input disabled class="draw-input" placeholder="单位cm" type="text"  v-model="combine.width"/></div>
          <div style="margin-bottom:10px"><span>高度：</span><input disabled class="draw-input" placeholder="单位cm" type="text"  v-model="combine.height"/></div>
          <div style="margin-bottom:10px"><span>价格：</span><input disabled class="draw-input" placeholder="单位:元" type="text"  v-model="combine.cost"/></div>
          <div style="margin-bottom:10px"><span style="font-weight:bold">安全距离</span></div>
          <div style="margin-bottom:10px"><span>上侧：</span><input  class="draw-input" placeholder="单位cm，默认为0" type="text"  v-model="combine.safe.up"/></div>
          <div style="margin-bottom:10px"><span>下侧：</span><input  class="draw-input" placeholder="单位cm，默认为0" type="text"  v-model="combine.safe.down"/></div>
          <div style="margin-bottom:10px"><span>左侧：</span><input  class="draw-input" placeholder="单位cm，默认为0" type="text"  v-model="combine.safe.left"/></div>
          <div style="margin-bottom:10px"><span>右侧：</span><input  class="draw-input" placeholder="单位cm，默认为0" type="text"  v-model="combine.safe.right"/></div>
           <div style="margin-bottom:10px"><span style="font-weight:bold">额外安全距离:</span></div>
          <div style="margin-bottom:10px"><span>操作面：</span><input style = "width:95px" class="image-url" placeholder="单位cm，默认为0" type="text"  v-model="combine.safe.operation"/></div>
          <div style="margin-bottom:10px"><span>背侧：</span><input  class="draw-input" placeholder="单位cm，默认为0" type="text"  v-model="combine.safe.back"/></div>          
      </div>
      <div style='text-align:center'>
          <button @click="changeNodeDetail" style='text-align:center; width:100px'>确定</button>
      </div>
    </Drawer>


    <div class="customize-win"  v-if="showCombineManageDialog">
        <div class="customize-win-header">
           <span>设备</span>
           <div @click.stop="showCombineManageDialog=false" class="close iconfont icon-guanbi"></div>
        </div>

        <div class="customize-image">
            <ul>
             <li draggable="true" v-for="(item,index) in combines" v-bind:key="index">
               <span @click.stop="removeCombine(item)" class="iconfont icon-guanbi"></span>
               <div style="margin-top:20px;font-size:12px;max-width:100px;overflow: hidden; text-overflow:ellipsis; white-space: nowrap;" v-html="item.name"></div>
            </li>
           </ul>
        </div>
    </div>

    <div class="customize-win" v-if="showObstacleDialog">
        <div class="customize-win-header">
           <span>障碍物</span>
           <div @click.stop="showObstacleDialog=false" class="close iconfont icon-guanbi"></div>
        </div>
        <div class="customize-image">
            <ul>
             <li draggable="true" v-for="(item,index) in obstacles" v-bind:key="index">
               <span @click.stop="removeObstacle(item)" class="iconfont icon-guanbi"></span>
               <!-- <img draggable="false" :src="item.image" alt=""> -->
               <div style="margin-top:20px;font-size:15px;max-width:50px;overflow: hidden; text-overflow:ellipsis; white-space: nowrap;" v-html="item.name"></div>
            </li>

             <li @click.stop="showObstacleDetail=!showObstacleDetail"><span class="iconfont icon-add" style="color:#666;font-size:48px;line-height:44px;text-align:center;cursor:pointer"></span></li>
           </ul>
        </div>
    </div>

    <Drawer title="障碍物" mask-style = "background-color:rgba(0,0,0,0)" width="200" v-model="showObstacleDetail">

        <Menu mode="horizontal" :theme="light" active-name="1" @on-select="changeOBMode">
          <MenuItem name="1">
              柱子
          </MenuItem>
          <MenuItem name="2">
              墙体
          </MenuItem>
        </Menu>
        
      <div class="upload-win-content"  style="margin-top:20px" v-if="selectOBMode=='1'">
          <div style="margin-bottom:20px"><span>宽度：</span><input  class="draw-input" placeholder="单位cm" type="text" v-model="obstacle.width"/></div>
          <div style="margin-bottom:20px"><span>高度：</span><input  class="draw-input" placeholder="单位cm" type="text" v-model="obstacle.height"/></div>
      </div>

      <div class="upload-win-content"  style="margin-top:20px" v-if="selectOBMode=='2'">
          <!-- <div style="margin-bottom:20px"><span>名称：</span><input  class="draw-input" placeholder="名字/编号" type="text" text="obstacleName" v-model="obstacle.name"/></div> -->
          <div style="margin-bottom:20px"><span>宽度：</span><input  class="draw-input" placeholder="单位cm" type="text" v-model="wall.width"/></div>
          <div style="margin-bottom:20px"><span>高度：</span><input  class="draw-input" placeholder="单位cm" type="text" v-model="wall.height"/></div>
      </div>

        <div class="upload-win-footer">
            <button @click="addObstacle">确定</button>
        </div>
    </Drawer>


    <div class="customize-win" v-if="showCustomize">
        <div class="customize-win-header">
           <span>房间</span>
           <div @click.stop="showCustomize=false" class="close iconfont icon-guanbi"></div>
        </div>

        <div class="customize-image">
            <ul>
             <li draggable="true" v-for="(item,index) in customize" v-bind:key="index">
               <span @click.stop="removeRoom(item)" class="iconfont icon-guanbi"></span>
               <!-- <img draggable="false" :src="item.image" alt=""> -->
               <div style="margin-top:20px;font-size:15px;max-width:50px;overflow: hidden; text-overflow:ellipsis; white-space: nowrap;" v-html="item.roomName"></div>
            </li>

             <li @click.stop="showUpload=!showUpload"><span class="iconfont icon-add" style="color:#666;font-size:48px;line-height:44px;text-align:center;cursor:pointer"></span></li>
           </ul>
        </div>
    </div>

    <Drawer mask-style = "background-color:rgba(0,0,0,0)" width="500" v-model="showUpload" class="facility-draw">
        <Menu mode="horizontal"  active-name="1" @on-select="changeRoomMode">
          <MenuItem name="1">
              基本
          </MenuItem>
          <MenuItem name="2">
              高级
          </MenuItem>
        </Menu>
        
      <div class="upload-win-content"  style="margin-top:20px" v-if="selectRoomMode=='1'">
          <div style="margin-bottom:20px"><span>宽度：</span><input  class="image-url" placeholder="单位cm" type="text" v-model="basicRoom.width"/></div>
          <div style="margin-bottom:20px"><span>高度：</span><input  class="image-url" placeholder="单位cm" type="text" v-model="basicRoom.height"/></div>
          <div style="margin-bottom:20px"><span style="padding-right:12px">房间名：</span><input id='basicRoomName' style="width:200px" class="image-url" placeholder="输入长宽自动生成" type="text" text="roomName" v-model="basicRoomName"/></div>
      </div>

        <div class="upload-win-content"  style="margin-top:20px" v-if="selectRoomMode=='2'">
        <div style="margin-bottom:20px"><span style="padding-right:12px">房间名：</span><input style="width:200px" class="image-url" placeholder="房间名" type="text" text="roomData" v-model="roomName"/></div>
        <div style="margin-bottom:20px">请按顺时针方向绘制参数: </div>
        <div style="text-align:center">
          <img :src="imgUrlRight" @click.stop="showRight=!showRight"/>
          <img :src="imgUrlDown" @click.stop="showDown=!showDown"/>
          <img :src="imgUrlLeft" @click.stop="showLeft=!showLeft"/>
          <img :src="imgUrlUp" @click.stop="showUp=!showUp"/>
        </div>
        <div style="margin-bottom:20px; text-align:center"> 
          <span style="margin-right:20px;"><Button @click="redoRoomData">撤销</Button></span>
          <span><Button @click="clearRoomData">重置</Button></span>
        </div>
        <div style="margin-bottom:20px">
          <span>房间参数：</span> 
          <span  v-text= "roomInput"></span> 
        </div>
      </div>
      <div class="upload-win-footer">
        <button @click="addRoom">确定</button>
      </div>
    </Drawer>

    <Drawer title="请输入向右的参数" mask-style = "background-color:rgba(0,0,0,0)" width="400" v-model="showRight" class="addroom-child-draw">
      <div class="upload-win-content">
          <div style="margin-bottom:20px"><span>实际长度：（若有倾斜角度时为垂直长度）</span><input  class="image-url" placeholder="单位cm" type="text" text="rightLength" v-model="lineLength"/></div>
          <div style="margin-bottom:20px">
           <span>倾斜角度：</span>
            <RadioGroup v-model="hasSlope">
                <Radio label="0">无</Radio>
                <Radio label="1">有</Radio>
            </RadioGroup>
          </div>
          <div style="margin-bottom:20px" v-if="hasSlope == 1"  >
            <div style="margin-bottom:20px">
              <span>偏移距离：</span><input  class="slope-length-input" v-model="slope.length" placeholder="默认不用填，斜线需要" type="text" text="name" />
              <img class = "tip-img" :src="imgUrlRightTip"/>
            </div>
            <RadioGroup v-model="slope.direction">
                <Radio label="0">逆时针</Radio>
                <Radio label="1">顺时针</Radio>
            </RadioGroup>
          </div>
      </div>
      <div class="upload-win-footer">
          <button @click="addRoomLine(0)">确定</button>
      </div>
    </Drawer>

    <Drawer title="请输入向下的参数" mask-style = "background-color:rgba(0,0,0,0)" width="400" v-model="showDown" class="addroom-child-draw">
      <div class="upload-win-content">
          <div style="margin-bottom:20px"><span>实际长度：（若有倾斜角度时为垂直长度）</span><input  class="image-url" placeholder="单位cm" type="text" text="rightLength" v-model="lineLength"/></div>
          <div style="margin-bottom:20px">
           <span>倾斜角度：</span>
            <RadioGroup v-model="hasSlope">
                <Radio label="0">无</Radio>
                <Radio label="1">有</Radio>
            </RadioGroup>
          </div>
          <div style="margin-bottom:20px" v-if="hasSlope == 1"  >
            <div style="margin-bottom:20px">
              <span>偏移距离：</span><input  class="slope-length-input" v-model="slope.length" placeholder="默认不用填，斜线需要" type="text" text="name" />
              <img class = "tip-img" :src="imgUrlLeftTip"/>
            </div>
            <RadioGroup v-model="slope.direction">
                <Radio label="0">逆时针</Radio>
                <Radio label="1">顺时针</Radio>
            </RadioGroup>
          </div>
      </div>
      <div class="upload-win-footer">
          <button @click="addRoomLine(1)">确定</button>
      </div>   
    </Drawer>

    <Drawer title="请输入向左的参数" mask-style = "background-color:rgba(0,0,0,0)" width="400" v-model="showLeft" class="addroom-child-draw">
      <div class="upload-win-content">
          <div style="margin-bottom:20px"><span>实际长度：（若有倾斜角度时为垂直长度）</span><input  class="image-url" placeholder="单位cm" type="text" text="rightLength" v-model="lineLength"/></div>
          <div style="margin-bottom:20px">
           <span>倾斜角度：</span>
            <RadioGroup v-model="hasSlope">
                <Radio label="0">无</Radio>
                <Radio label="1">有</Radio>
            </RadioGroup>
          </div>
          <div style="margin-bottom:20px" v-if="hasSlope == 1"  >
            <div style="margin-bottom:20px">
              <span>偏移距离：</span><input  class="slope-length-input" v-model="slope.length" placeholder="默认不用填，斜线需要" type="text" text="name" />
              <img class = "tip-img" :src="imgUrlRightTip"/>
            </div>
            <RadioGroup v-model="slope.direction">
                <Radio label="0">逆时针</Radio>
                <Radio label="1">顺时针</Radio>
            </RadioGroup>
          </div>
      </div>
      <div class="upload-win-footer">
          <button @click="addRoomLine(2)">确定</button>
      </div>   
  </Drawer>

    <Drawer title="请输入向上的参数" mask-style = "background-color:rgba(0,0,0,0)" width="400" v-model="showUp" class="addroom-child-draw">
      <div class="upload-win-content">
          <div style="margin-bottom:20px"><span>请输入向上的参数</span></div>
          <div style="margin-bottom:20px"><span>实际长度：（若有倾斜角度时为垂直长度）</span><input  class="image-url" placeholder="单位cm" type="text" text="rightLength" v-model="lineLength"/></div>
          <div style="margin-bottom:20px">
           <span>倾斜角度：</span>
            <RadioGroup v-model="hasSlope">
                <Radio label="0">无</Radio>
                <Radio label="1">有</Radio>
            </RadioGroup>
          </div>
          <div style="margin-bottom:20px" v-if="hasSlope == 1"  >
            <div style="margin-bottom:20px">
              <span>偏移距离：</span><input  class="slope-length-input" v-model="slope.length" placeholder="默认不用填，斜线需要" type="text" text="name" />
              <img class = "tip-img" :src="imgUrlLeftTip"/>
            </div>
            <RadioGroup v-model="slope.direction">
                <Radio label="0">逆时针</Radio>
                <Radio label="1">顺时针</Radio>
            </RadioGroup>
          </div>
      </div>
      <div class="upload-win-footer">
          <button @click="addRoomLine(3)">确定</button>
      </div>   
  </Drawer>
  </div>
</template>
<script>

import eventBus from '../eventbus';
import FacilitiesList from '../../public/Config';

export default {
  name: 'Toolbar',
  data(){
     return{
        currentPanel : 1,
        //room
         selectRoomMode : 1,
        customize:[],
        showCustomize:false,
        showUpload:false,
        showRight:false,
        showDown:false,
        showLeft:false,
        showUp:false,
        hasSlope : "0", //"0"-逆时针 "1"-顺时针
        roomData:[],
        roomInput : '',
        roomName:'',
        currentScale: 1,
        savedScale: 1,
        lineLength : '',
        basicRoom: {
          width:'',
          height:''
        },
        //斜线参数
        slope : {
          length: '',
          direction: '0'
        },
        //obstacle
        selectOBMode : 1,
        obstacles:[],
        //柱子
        obstacle : {
          width : '',
          height : '',
          name : '柱子',
          kind: 2,
        },
        //墙体
        wall : {
          width : '',
          height : '30',
          name : '墙',
          kind: 2,
        },
        showObstacleDialog: false,
        showObstacleDetail: false,
        //facility
        facilities:[],
        facility : {
          kind : 3,
          name:'',
          id: '',
          width:'',
          height:'',
          cost:'',
          safe: {
            up:'',
            right:'',
            down:'',
            left:'',
            operation:'',
            back: ''
          },
          //0123，上右下左
          face: "0",
          group:"0"
        },
        showFacilityManageDialog: false,
        showFacilityDialog: false,
        currentFacilityGroup : 0,
        facilityDetail: {
          isShow: false,
          mode: 0
        },
        combineDetail: {
          isShow: false,
        },
        serchKeyWord:'',
        showSearchDialog: false,
        currntSelectedNode: null,
        //combine
        showCombineManageDialog: false,
        combine: {
          name : '',
          width: '',
          height: '',
          nodes : [],
          cost : '',
          safe: {
            up:'',
            right:'',
            down:'',
            left:'',
            operation:'',
            back: ''
          }
        },
        combines: [],
        shape:[
          {
            id:'flowChart',
            header:'设备',
            icon:[
              {
                path:'<svg style="left: 1px; top: 1px; width: 32px; height: 30px; display: block; position: relative; overflow: hidden;"><g><g></g><g><g transform="translate(0.5,0.5)" style="visibility: visible;"><rect x="1.44" y="7.68" width="28.8" height="14.4"  fill="#ffffff" stroke="#000000" stroke-width="1.3" pointer-events="all"></rect></g></g><g></g><g></g></g></svg>',
                iconName:'',
                text:'rounded rectangle',
                command:'rectangle'
              },
              {
                path:'<svg style="left: 1px; top: 1px; width: 32px; height: 30px; display: block; position: relative; overflow: hidden;"><g><g></g><g><g transform="translate(0.5,0.5)" style="visibility: visible;"><rect x="0.73" y="7.3" width="29.2" height="14.6" fill="none" stroke="white" pointer-events="stroke" visibility="hidden" stroke-width="9"></rect><rect x="0.73" y="7.3" width="29.2" height="14.6" fill="none" stroke="none" pointer-events="all"></rect></g><g style=""><g transform="translate(7,10)scale(0.73)"><foreignObject style="overflow:visible;" pointer-events="all" width="22" height="12"><div style="display: inline-block; font-size: 12px; font-family: Helvetica; color: rgb(0, 0, 0); line-height: 1.2; vertical-align: top; width: 24px; white-space: normal; overflow-wrap: normal; text-align: center;"><div xmlns="http://www.w3.org/1999/xhtml" style="display:inline-block;text-align:inherit;text-decoration:inherit;white-space:normal;">Text</div></div></foreignObject></g></g></g><g></g><g></g></g></svg>',
                iconName:'',
                text:'text',
                command:'text'
              },
              {
                path:'<svg style="left: 1px; top: 1px; width: 32px; height: 30px; display: block; position: relative; overflow: hidden;"><g><g></g><g><g transform="translate(0.5,0.5)" style="visibility: visible;"><rect x="1.44" y="7.68" width="28.8" height="14.4" fill="#ffffff" stroke="#000000" stroke-width="1.3" pointer-events="all"></rect><path d="M 4.32 7.68 L 4.32 22.08 M 27.36 7.68 L 27.36 22.08" fill="none" stroke="white" stroke-width="9.3" stroke-miterlimit="10" pointer-events="stroke" visibility="hidden"></path><path d="M 4.32 7.68 L 4.32 22.08 M 27.36 7.68 L 27.36 22.08" fill="none" stroke="#000000" stroke-width="1.3" stroke-miterlimit="10" pointer-events="all"></path></g></g><g></g><g></g></g></svg>',
                iconName:'',
                text:'process',
                command:'preRect'
              },
            ]
          }
        ],
        imgUrlRight:require("../img/right.png"),
        imgUrlDown:require("../img/down.png"),
        imgUrlLeft:require("../img/left.png"),
        imgUrlUp:require("../img/up.png"),
        imgUrlLeftTip:require("../img/leftTip.png"),
        imgUrlRightTip:require("../img/rightTip.png"),  
     }
  },
  computed:{
    button1Statu() {
      if(!this.showFacilityDialog) return "";
      return this.currentFacilityGroup === 0 ? "primary" : "";
    },
    button2Statu() {
      if(!this.showFacilityDialog) return "";
      return this.currentFacilityGroup === 1 ? "primary" : "";
    },
    button3Statu() {
      if(!this.showFacilityDialog) return "";
      return this.currentFacilityGroup === 2 ? "primary" : "";
    },
    precustomize(){
        return this.customize.slice().splice(0,100);
     },
    preObstacle(){
        return this.obstacles.slice().splice(0,100);
     },
    preFacilities(){
      var group = this.currentFacilityGroup;
      var facilities = this.facilities.filter(f=>{
        if(f.group == group) return true;
        if(f.group == undefined && group === 2) return true;
        return false;
      });

      if(this.serchKeyWord === '') {
        return facilities.slice();
      }
      let res = [];
      for(let i=0; i<facilities.length; i++) {
        if(facilities[i].name.startsWith(this.serchKeyWord) || facilities[i].id.startsWith(this.serchKeyWord)) {
          res.push(facilities[i]);
        }
      }
      return res;
     },
     basicRoomName() {
       if(this.basicRoom.width && this.basicRoom.height) {
         return this.basicRoom.width + ' × ' +  this.basicRoom.height;
       } else {
         return '';
       }
     },
     facilityGroup0() {
       return this.facilities.filter(f=>{return f.group == 0;});
     },
     facilityGroup1() {
       return this.facilities.filter(f=>{return f.group == 1;});
     },
     facilityGroup2() {
       return this.facilities.filter(f=>{return f.group != 0 && f.group != 1;});
     }
  },

  mounted(){
    var me = this;
    var strRoom=localStorage.getItem('customize-icon');
    var strObstacles=localStorage.getItem('obstacles-icon');
    var strFacilities=localStorage.getItem('facilities-icon');
    var strCombines=localStorage.getItem('combines-icon');
    try{
      if(strRoom){
         var dataRoom=JSON.parse(strRoom);
         this.customize=dataRoom;
      }

      if(strCombines){
         this.combines=JSON.parse(strCombines);
      }
      var door = {
        width : '60',
        height : '60',
        name : '门',
        kind : 1
      }
      door.path = this.buildPath(door);
      var dataOb= strObstacles ? JSON.parse(strObstacles) : [];
      this.obstacles=[door, ...dataOb];

      var dataFa= strFacilities ? JSON.parse(strFacilities) : [];
      var lists = FacilitiesList;
      for(let i=0; i<lists.length; i++) {
        lists[i].path = this.buildPath(lists[i]);
        dataFa.push(lists[i]);
      }
      this.facilities=dataFa;
      
    }catch(e){
       window.console.log(e);
    }

    eventBus.$on('addCombine',(e)=>{
      var node = e.node;
      node.path = me.buildPath(node);
      me.combines.unshift(node);
      me.storeCombines();
      me.currentPanel = "3";
      this.showFacilityDialog =false;
    })

    eventBus.$on('closeCombine',()=>{
      this.currentScale = this.savedScale;
    })

    eventBus.$on('deleteAll',()=>{
      this.currentScale = 1;
    });

    eventBus.$on('showInfoDrawWithNode',(e)=>{
      this.showFacilityMsg(e.node);
    });
  },
  methods:{

    changeRoomMode(name) {
      this.selectRoomMode = name;
      this.roomData=[];
    },

    changeOBMode(mode) {
      this.selectOBMode = mode;
    },
    clearSearch () {
      this.serchKeyWord = '';
    },

    addFacilityDetail() {
      this.facilityDetail.isShow = !this.facilityDetail.isShow;
      this.facilityDetail.mode = 0;
    },

    showCombinePanel () {
      this.currentFacilityGroup = 0;
      this.showFacilityDialog =true;
      this.currentPanel = 2;
      this.showCombine = !this.showCombine;
      this.savedScale = this.currentScale;
      this.currentScale = 1;
      eventBus.$emit('showCombine', {isShow : true});
    },

    showFacilityMsg (node) {
      if(node.kind == '3') {
        this.currntSelectedNode = node;
        var data = node.originData ? node.originData : node;
        if(node.originData && node.originData.isCombine) {
          this.combine = JSON.parse(JSON.stringify(data))
          this.combineDetail.isShow = true;
          //安全距离要从node中获取，因为修改safe的不会改变origindata
          Object.assign(this.combine.safe, node.safe)
        } else {
          this.facility = JSON.parse(JSON.stringify(data))
          //Object.assign(this.facility, data);
          this.facilityDetail.isShow = true;
          this.facilityDetail.mode = 1;
          //安全距离要从node中获取，因为修改safe的不会改变origindata
          Object.assign(this.facility.safe, node.safe)
        }
      }
    },

    updateFacilityDialog(group) {
      if(!this.showFacilityDialog) {
        this.showFacilityDialog = true;
      } else if(this.currentFacilityGroup == group) {
        this.showFacilityDialog = !this.showFacilityDialog;
      }
      this.currentFacilityGroup = group;
    },

    command(p){
      this.currentPanel = p;
    },


    redoRoomData () {
      this.roomData.pop();
      this.updateRoomInput();
    },

    clearRoomData() {
        this.roomData=[];
        this.basicRoom.width = '';
        this.basicRoom.height = '';
        this.roomInput = "";
        this.roomName='';
    },

    addRoom(){
      var basic = document.getElementById('basicRoomName');
      if(this.selectRoomMode == "1" && this.basicRoom.width !='' && this.basicRoom.height != '' && basic.value !='') {
        this.roomData.push({
          direction : 0,
          length : this.basicRoom.width,
          hasSlope : false
        });
        this.roomData.push({
          direction : 1,
          length : this.basicRoom.height,
          hasSlope : false
        });
        this.roomData.push({
          direction : 2,
          length : this.basicRoom.width,
          hasSlope : false
        });
        this.roomData.push({
          direction : 3,
          length : this.basicRoom.height,
          hasSlope : false
        });
        var basicObj={
           roomName : basic.value,
           roomData : this.roomData 
         };
        this.customize.unshift(basicObj);
        this.storeRoom();
        this.showUpload=false;
        this.showCustomize=false;
      } else if(this.roomData.length > 0 && this.roomName){
         var obj={
           roomName : this.roomName,
           roomData : this.roomData 
         };
         this.customize.unshift(obj);
         this.storeRoom();
      }
      this.showUpload=false;
      this.showCustomize=false;
      this.clearRoomData();
    },
    
    addObstacle() {
      if((this.obstacle.width && this.obstacle.height) || (this.wall.width && this.wall.height)){
      var ob = this.selectOBMode === 1 ? this.obstacle : this.wall;
      var obj = JSON.parse(JSON.stringify(ob));
         obj.path = this.buildPath(ob);
         this.obstacles.splice(1,0,obj);
         //this.storeObstacles();
         this.showObstacleDetail=false;
         this.showObstacleDialog=false;
         if(this.selectOBMode === 1) {
          this.obstacle.width='';
          this.obstacle.height='';
         } else {
           this.wall.width = '';
         }
      }
    },

    addFacility() {
      if(this.facility.name && this.facility.width && this.facility.height) {
         var obj = JSON.parse(JSON.stringify(this.facility));
         //obj.path = this.buildPath(this.facility);
         obj.safe.left = this.facility.safe.left=='' ?  0 : Number.parseFloat(this.facility.safe.left);
         obj.safe.right = this.facility.safe.right=='' ?  0 : Number.parseFloat(this.facility.safe.right);
         obj.safe.up = this.facility.safe.up=='' ?  0 : Number.parseFloat(this.facility.safe.up);
         obj.safe.down = this.facility.safe.down=='' ?  0 : Number.parseFloat(this.facility.safe.down);
         obj.width =  Number.parseFloat(this.facility.width)
         obj.height =  Number.parseFloat(this.facility.height)
         obj.cost =  Number.parseFloat(this.facility.cost)
         this.facilities.unshift(obj);
         //this.storeFacilities();
         this.facilityDetail.isShow=false;
         this.showFacilityManageDialog=false;
         this.clearFacility();
      }
    },

    changeNodeDetail() {
      if(!this.currntSelectedNode) return;
      var dirtyNode = this.currntSelectedNode.originData.isCombine ? this.combine : this.facility;
      this.currntSelectedNode.safe.left = Number.parseFloat(dirtyNode.safe.left);
      this.currntSelectedNode.safe.right = Number.parseFloat(dirtyNode.safe.right);
      this.currntSelectedNode.safe.up = Number.parseFloat(dirtyNode.safe.up);
      this.currntSelectedNode.safe.down = Number.parseFloat(dirtyNode.safe.down);
      this.currntSelectedNode.safe.operation = Number.parseFloat(dirtyNode.safe.operation);
      this.currntSelectedNode.safe.back = Number.parseFloat(dirtyNode.safe.back);
      this.facilityDetail.isShow = false;
      this.combineDetail.isShow = false;
    },

    clearFacility() {
      this.facility.name = '';
      this.facility.id = '';
      this.facility.width = '';
      this.facility.height = '';
      this.facility.face = '0';
      this.facility.safe.up = '';
      this.facility.safe.down = '';
      this.facility.safe.left = '';
      this.facility.safe.right = '';
      this.facility.safe.operation = '';
      this.facility.safe.back = '';
      this.facility.cost = '';
      this.facility.group = '0';
    },

    buildParameter (f) {
      return f.width + ' × ' + f.height + ' cm';
    },

    buildPath : function(f) {
      var buildName = function (name) {
          if(name.length > 10) {
            return name.slice(0, 5) + '...';
          } else {
            return name
          }
      };
      var buildParameter = function (f) {
          return f.width + ' × ' + f.height + ' cm';
      };
      var width = 90, height = 35, paramH = 70;
      if(f.kind === 1) {
        height = 20
      }
      if(f.kind === 2) {
        height = 35
      }
      if(f.kind === 2) {
        height = 35;
        paramH = 55;

      }
      if(f.kind === 5) {
        height = 35
      }
      if(f) {
        if(f.kind === 1) {
          let nameStr = '<text x="35" y="25" fill="black" font-size="16">'+ '门'+'</text>';
          let paramStr = '<text x="2" y="45" fill="black" font-size="16">'+ buildParameter(f) +'</text>';
          return '<svg style="left: 1px; top: 1px; width: 100px; height: 50px; display: block; position: relative; overflow: hidden;"><g><g></g><g><g transform="translate(0.5,0.5)" style="visibility: visible;"><rect x="1.44" y="7.68" width="' + width + '" height="' + height +'"  fill="#ffffff" stroke="#000000" stroke-width="1.3" pointer-events="all"></rect>' + nameStr + paramStr + '</g></g><g></g><g></g></g></svg>';
        }

        let nameStr = '<text x="45" y="25" fill="black" font-size="16" style="dominant-baseline:middle;text-anchor:middle">'+ buildName(f.name) +'</text>';
        let idStr = '';
        if(f.id) {
          idStr = '<text x="45" y="55" fill="black" font-size="16" style="dominant-baseline:middle;text-anchor:middle">'+ buildName(f.id) +'</text>';
        }
        let paramStr = '<text x="45" y="'+ paramH +'" fill="black" font-size="14" style="dominant-baseline:middle;text-anchor:middle">'+ buildParameter(f) +'</text>';
        return '<svg style="left: 1px; top: 1px; width: 100px; height: 90px; display: block; position: relative; overflow: hidden;"><g><g></g><g><g transform="translate(0.5,0.5)" style="visibility: visible;"><rect x="1.44" y="7.68" width="' + width + '" height="' + height +'"  fill="#ffffff" stroke="#000000" stroke-width="1.3" pointer-events="all"></rect>' + nameStr + idStr + paramStr + '</g></g><g></g><g></g></g></svg>';
      }
    },

    updateRoomInput : function() {
        var roomData = this.roomData;
        var str = '';
        var dirMap = {
          0 : '右',
          1 : '下',
          2 : '左',
          3 : '上'
        };
        roomData.forEach(r => {
          let sDir = r.slopeDirection === '1' ? '+' : '-';
          let sStr = !r.hasSlope ? '' : '(' + '偏' + sDir +  r.slopeLength + ')';
          str += dirMap[r.direction] + ' : ' + r.length + sStr + '    ';
        });
        this.roomInput = str;
    },

    addRoomLine : function(direction) {
      if(this.lineLength !== '') {
          this.roomData.push({
          direction : direction,
          length : this.lineLength,
          hasSlope : this.hasSlope === '1',
          slopeLength : this.slope.length,
          slopeDirection : this.slope.direction
        })
      }
      this.hasSlope = '0';
      this.slope.length = '';
      this.lineLength = '';
      this.showRight = false;
      this.showDown = false;
      this.showLeft = false;
      this.showUp = false;
      this.updateRoomInput();
    },
    faciliyOkBtn : function() {
       if(this.facilityDetail.mode == 0) {
         this.addFacility();
       } else if(this.facilityDetail.mode == 1) {
         this.changeNodeDetail();
       }
    },
    storeRoom(){
      localStorage.setItem('customize-icon',JSON.stringify(this.customize));
    },
    storeObstacles(){
      localStorage.setItem('obstacles-icon',JSON.stringify(this.obstacles));
    },
    storeFacilities() {
      localStorage.setItem('facilities-icon',JSON.stringify(this.facilities));
    },
    storeCombines() {
      localStorage.setItem('combines-icon',JSON.stringify(this.combines));
    },
    removeRoom(item){
      var index=this.customize.indexOf(item);
      if(index>-1){
         this.customize.splice(index,1);
         this.storeRoom();
      }
    },

    removeObstacle(item){
      var index=this.obstacles.indexOf(item);
      if(index>-1){
         this.obstacles.splice(index,1);
         this.storeObstacles();
      }
    },

    removeFacility(item){
      var index=this.facilities.indexOf(item);
      if(index>-1){
         this.facilities.splice(index,1);
         this.storeFacilities();
      }
    },

    removeCombine(item){
      var index=this.combines.indexOf(item);
      if(index>-1){
         this.combines.splice(index,1);
         this.storeFacilities();
      }
    },

    getRoomScale (roomData) {
      var offSetX = 0,
          offSetY = 0,
          box = {
            x1:9999,
            y1:9999,
            x2:-9999,
            y2:-9999,
          };
      var tempRes = this.buildRoomCommand(roomData, {
        scale : 1,
        'offSetX':0,
        'offSetY' : 0
      }).filter(item=>item.command=='line')

      for(let i=0; i<tempRes.length; i++) {
        box.x1 = Math.min(tempRes[i].shape.x1,box.x1);
        box.x2 = Math.max(tempRes[i].shape.x2,box.x2);
        box.y1 = Math.min(tempRes[i].shape.y1,box.y1);
        box.y2 = Math.max(tempRes[i].shape.y2,box.y2);
      }

      var windowWidth = window.innerWidth - 200; 
      var windowHeight = window.innerHeight - 200; 

      var boxWidth = box.x2-box.x1,
          boxHeight = box.y2-box.y1;
      

      var scale = Math.max(boxWidth/windowWidth, boxHeight/windowHeight) * 1.1;

      var adjust = boxWidth/windowWidth > boxHeight/windowHeight ? 1.1 : 0.9;

      scale=adjust*scale;
      var boxCenter = {
        x : boxWidth/2,
        y : boxHeight/2,
      }
      var screenCenter = {
        x : windowWidth/2,
        y : windowHeight/2,
      }

      offSetX = Math.abs(screenCenter.x - boxCenter.x/scale);
      offSetY = Math.abs(screenCenter.y - boxCenter.y/scale);
      eventBus.$emit('updateOffsetX', {x : offSetX, y:offSetY});
      return {
        scale : scale,
        'offSetX' : offSetX,
        'offSetY' : offSetY
      };
    },

    buildRoomCommand(roomData, scaleObj){
        var res = [];
        var lines = roomData;
        var scale = scaleObj.scale;
        var startX = 0 + scaleObj.offSetX;
        var startY = 50 + scaleObj.offSetY;
        var lastX = startX;
        var lastY = startY;
        var lineWeight = 2;
        for(let i=0; i<lines.length; i++) {
            var lineCmd = {
              command:'line',
              kind: 0,
              scale: scale
            }
            var current = lines[i];
            var direct = current.direction;
            var origniaLength = current.length;
            var length = Number.parseFloat(origniaLength)/scale;
            var shape = {
              x1 : lastX,
              y1 : lastY
            }
            var textCmd = {
              style: {
                  text:  Number.parseInt(origniaLength) + 'cm',
                  fontSize: 15,
              },
              command:'text',
              position:[0,0],
              draggable: false,
            }
            if(textCmd.style.text >= 6) textCmd.style.fontSize = 12;
            lineCmd.text = length;
            switch(direct) {
              case 0 :
                if(!current.hasSlope) {
                  shape.x2 =  shape.x1 + length;
                  shape.y2 = shape.y1;
                } else {
                  //(l-x1)^2 + slopeLength^2 = length^2  
                  let sLength = Number.parseFloat(current.slopeLength)/scale;
                  let verticalLength = Math.sqrt(Math.pow(length, 2) - Math.pow(sLength, 2));
                  shape.x2 =  shape.x1 + verticalLength;
                  if(current.slopeDirection === '0') {
                    shape.y2 = shape.y1 - sLength;
                  } else {
                    shape.y2 = shape.y1 + sLength;
                  }
                }
                textCmd.x = (shape.x1 + shape.x2)/2 - 25;
                textCmd.y = (shape.y1 + shape.y2)/2 - 25;
                lineCmd.direction = 0;
                break;
              case 1 :
                if(!current.hasSlope) {
                  shape.y1 -= lineWeight;
                  shape.x2 =  shape.x1;
                } else {
                  //(l-x1)^2 + slopeLength^2 = length^2
                  let sLength = Number.parseFloat(current.slopeLength)/scale;
                  let verticalLength = Math.sqrt(Math.pow(length, 2) - Math.pow(sLength, 2));
                  shape.y2 =  shape.y1 + verticalLength;
                  if(current.slopeDirection === '0') {
                    shape.x2 = shape.x1 + sLength;
                  } else {
                    shape.x2 = shape.x1 - sLength;
                  }
                }
                shape.y2 = shape.y1 + length + lineWeight;
                textCmd.x = (shape.x1 + shape.x2)/2 + 5;
                textCmd.y = (shape.y1 + shape.y2)/2;
                lineCmd.direction = 1;
                break;              
              case 2 :
                if(!current.hasSlope) {
                  shape.x2 =  shape.x1 - length;
                  shape.y2 = shape.y1;
                } else {
                  //(l-x1)^2 + slopeLength^2 = length^2
                  let sLength = Number.parseFloat(current.slopeLength)/scale;
                  let verticalLength = Math.sqrt(Math.pow(length, 2) - Math.pow(sLength, 2));
                  shape.x2 =  shape.x1 - verticalLength;
                  if(current.slopeDirection === '0') {
                    shape.y2 = shape.y1 + sLength;
                  } else {
                    shape.y2 = shape.y1 - sLength;
                  }
                }
                textCmd.x = (shape.x1 + shape.x2)/2 - 25;
                textCmd.y = (shape.y1 + shape.y2)/2 + 5;
                lineCmd.direction = 2;
                break;
               case 3 :
                if(!current.hasSlope) {
                  shape.y1 += lineWeight;
                  shape.x2 =  shape.x1 ;
                } else {
                  //(l-x1)^2 + slopeLength^2 = length^2
                  let sLength = Number.parseFloat(current.slopeLength)/scale;
                  let verticalLength = Math.sqrt(Math.pow(length, 2) - Math.pow(sLength, 2));
                  shape.y2 =  shape.y1 - verticalLength;
                  if(current.slopeDirection === '0') {
                    shape.x2 = shape.x1 - sLength;
                  } else {
                    shape.x2 = shape.x1 + sLength;
                  }
                }
                shape.y2 = shape.y1 - length - lineWeight;
                textCmd.x = (shape.x1 + shape.x2)/2 - 60;
                textCmd.y = (shape.y1 + shape.y2)/2 - 2;
                lineCmd.direction = 3;
                break;                
            }
            if(current.hasSlope) {
              lineCmd.slopeDirection = current.slopeDirection;
            }
            lineCmd.shape = shape;
            res.push(lineCmd);
            res.push(textCmd);
            lastX = shape.x2;
            lastY = shape.y2;
        }
      return res; 
    },

    buildObstacleCommand(item){
      if(!item) return;
      var scale = this.currentScale,
          w = Number.parseFloat(item.width)/scale,
          h = Number.parseFloat(item.height)/scale,
          name = item.name,
          fillColor = item.kind === 1 ? 'white' : '#A9C4EB',
          lineWidth = item.kind === 1 ? 0 : 1,
          fontSize = item.kind === 1 ? 20 : 12;
      return {
        style: {
            fill: fillColor,
            stroke: '#333',
            lineWidth: lineWidth,
            text: name,
            textFill: '#333',
            fontWeight: 'normal',
            fontStyle: '',
            textPosition: 'inside',
            fontSize: fontSize,
            opacity:1
        },
        kind : item.kind,
        shape: {
            width: w,
            height: h
        },
        z: item.kind === 1 ? 999 : 200,
        draggable: true,
        originData : item,
        command: "rectangle"
      }
    },

    buildFacilityCommand(item){
      if(!item) return;
      var scale = this.currentScale,
          w = Number.parseFloat(item.width)/scale,
          h = Number.parseFloat(item.height)/scale,
          name = item.name,
          face = item.face;
      return {
        style: {
            fill: '#fff',
            stroke: '#333',
            lineWidth: 1,
            text: name,
            textFill: '#333',
            fontWeight: 'normal',
            fontStyle: '',
            textPosition: 'inside',
            fontSize: 12,
            opacity:1
        },
        safe: {
          up : Number.parseFloat(item.safe.up),
          down : Number.parseFloat(item.safe.down),
          left : Number.parseFloat(item.safe.left),
          right : Number.parseFloat(item.safe.right),
          operation : Number.parseFloat(item.safe.operation),
          back : Number.parseFloat(item.safe.back)
        },
        face: face,
        kind : 3,
        shape: {
            width: w,
            height: h
        },  
        z: 200,
        originData : item,
        cost : item.cost,
        name : item.name,
        draggable: true,
        command: "preRect"
      }
    },


    buildCombineCommand(item){
      if(!item) return;
      return this.combineCommandHelper(item, this.currentScale);
    },

    dragstart(e,item){
      //0 墙体
      //2 障碍物
      //3 设备
      //4 警示线
      //5 组合
      var cmd = '';
      if(item.roomData) {
        var s = this.getRoomScale(item.roomData);
        cmd = JSON.stringify(this.buildRoomCommand(item.roomData, s));
        this.currentScale = s.scale;
      } else if(item.kind === 2 || item.kind === 1){
        cmd = JSON.stringify(this.buildObstacleCommand(item));
      } else if(item.kind === 3){
        cmd = JSON.stringify(this.buildFacilityCommand(item));
      } else if(item.kind === 4){ 
        cmd = JSON.stringify(this.buildCombineCommand(item));
      } else if(item.kind === 5){ 
        cmd = JSON.stringify(this.buildCombineCommand(item));
      }
      if(cmd !== '') {
        e.dataTransfer.setData("addShape", cmd);
      }
    },

    uploadImage(e){
      var file=e.target.files[0];
      if(file){
       var type=file.type;
       var formData=new FormData();
       formData.append('smfile',file);
       if(['image/jpeg','image/png'].indexOf(type)>-1){
           this.ajax({
             method:'POST',
             url:'https://sm.ms//api/v2/upload',
             body:formData,
             succseeFn(res){
               window.console.log(res);
             },
             failFn(xhr){
               window.console.log(xhr);
             }

           })
       }
      }
    },
    ajax :function(options){
          //给参数一个选项
          let method = options.method
          let url = options.url
          let body = options.body
          let succseeFn = options.succseeFn
          let failFn = options.failFn

          let request = new XMLHttpRequest();
          //初始化请求
          request.open(method, url,true);
          request.setRequestHeader("Content-type","multipart/form-data");
          request.setRequestHeader('Authorization','');
          request.onreadystatechange = () => {
              if (request.readyState === 4) {
                  if (request.status >= 200 && request.status < 300) {
                      succseeFn.call(undefined, request.responseText)
                  } else if (request.status >= 400) {
                      failFn.call(undefined, request)
                  }
              }
          }
          request.send(body);
   }
 }

}
</script>

