<template>
  <div>
        <div class="editor" id="editor" @dragover="dragover" @dragstart="dragstart" @drop="drop">
            <div id="flowEditor"></div>
            <div class="costtable" v-show = "dowloadDetail.isShow">
              <div style="font-weight:blod;text-align:center;font-size:20px;margin:10px 10px 10px 0" v-html="dowloadDetail.name"></div>
              <Table :columns="columns" :data="datas"></Table>
            </div> 
        </div>
        <Edit class="text-editor" ref='edit' v-show="editModel" v-model="text" @blurFunc="updateText" :style="{left:left+'px',top:top+'px','min-width':width+'px','min-height':height+'px'}"></Edit>
        <div> <Combine /> </div>
        <Drawer title="导出设计图"  v-model="dowloadDetail.isShow"  width="290">
          <div class="upload-win-content">
              <div style="margin-bottom:10px"> <span style="color:red">*</span><span>请输入项目标题：</span><input placeholder="" type="text"  v-model="dowloadDetail.name"/></div>
              <div style="margin-bottom:10px">
              <span>已用设备：</span>
              <!-- <List style="margin-left:40px">
                <ListItem  v-for="item in summary" v-bind:key="item.name">
                  <span v-html="item.name" :title="item.text"></span>
                  <span style="margin-left:10px" v-html="item.cost + '元'" :title="item.cost"></span>
                  <span style="margin-left:10px" v-html="item.count + '个'" :title="item.count"></span>
                </ListItem>
              </List > -->
              <Table :columns="columns" :data="datas"></Table>
              <div style="float:right;font-size:20px;"><span>总计：</span> <span v-text="summary + '元'"></span></div>
              </div>
          </div>
          <div class="upload-win-footer">
              <button @click="download('png')">保存</button>
          </div>
        </Drawer>

        <Drawer title="多选模式" :closable="false" :mask-closable="false" :mask="false" width="130" v-model="showMultiSelectPanel">
          <div style="margin-bottom:10px"><span>请依次选择设备</span></div>

          <div style="margin-bottom:20px">
            <span>对齐方式：</span>
            <RadioGroup v-model="alignType" >
                <Radio label="1">操作面</Radio>
                <Radio label="0">背面</Radio>
            </RadioGroup>
         </div> 
          <div style="margin-bottom:20px">
              <Button @click="buildCombine()">组合</Button>
          </div>
          <div style="margin-bottom:20px">
              <Button @click="resetMultiSelectMode()">重选</Button>
          </div>
          <div style="margin-bottom:20px">
              <Button @click="exitMultiSelectMode()">退出</Button>
          </div>
          <!-- <div>
              <Button @click="buildCombine()">还原</Button>
          </div>
          <div>
              <Button @click="buildCombine()">确定</Button>
          </div> -->
        </Drawer>
<!-- 
        <div id="design" class="design" >
          <Table :columns="columns" :data="datas"></Table>
          <img id="designImg" style="width:500px;height:500px">
        </div>
        <div class="final" v-if="showDesign">
            <div class="customize-win-header">
              <span>设计图</span>
           <div @click.stop="showDesign=false" class="close iconfont icon-guanbi"></div>
          </div>
         <img id="final">
         </div> -->
         <img id="final">

  </div>
</template>

<script>
import flowEditor from '../flow/flowEditor';
import eventBus from '../eventbus';
import Edit from './plugin/edit';
import zrender from 'zrender';
import Combine from './Combine'
import html2canvas from 'html2canvas';

import getPlainTxt from '../flow/ued/getPlainTxt';

export default {
  name: 'Editor',
  components:{
    Edit,
    Combine
  },
  data(){
     return{
        left:0,
        top:0,
        width:0,
        height:0,
        text:'',
        plainTxt:'',
        editModel:false,
        icon:[],
        colors:'#194d33',
        show:false,
        edge:null,
        showInfoDraw: false,
        showMultiSelectPanel: false,
        offsetX: 0,
        alignType : "1",
        dowloadDetail: {
          isShow : false,
          name : ''
        },
         columns: [
          {
              title: '设备名',
              key: 'name'
          },
          {
              title: '数量(个)',
              key: 'count'
          },
          {
              title: '花费(元)',
              key: 'cost'
          }
        ],
        datas:[],
        summary : 0,
     }
  },
  mounted(){
     var that=this;
     this.editor=new flowEditor('flowEditor');
     eventBus.$on('leftAlign',()=>{
       this.editor.leftAlign();
     });
     eventBus.$on('rightAlign',()=>{
       this.editor.rightAlign();
     });
     eventBus.$on('horizontalAlign',()=>{
       this.editor.horizontalAlign();
     });
     eventBus.$on('verticalAlign',()=>{
       this.editor.verticalAlign();
     });
      eventBus.$on('createGroup',()=>{
       this.editor.createGroup();
     });
     eventBus.$on('redo',()=>{
       this.editor.redo();
     });
     eventBus.$on('undo',()=>{
       this.editor.undo();
     });
      eventBus.$on('undo',()=>{
       this.editor.undo();
     });
     eventBus.$on('updateOffsetX',(e)=>{
       this.offsetX = e.x;
       this.offsetY = e.y;
     });  
      eventBus.$on('showInfoDraw',()=>{
       if(this.status=='selectNode'){
            eventBus.$emit('showInfoDrawWithNode',{node:this.node});
        }
     });
      eventBus.$on('rotate',()=>{
        if(this.status=='selectNode'||this.status=='selectNodes'){
            this.editor.rotate('node',this.node);
            //eventBus.$emit('clearSelect');  
        }
     });

     
     eventBus.$on('initFlow',(e)=>{
       try{
          this.editor.clearHistory();
          this.editor.refreshHistory();
          this.editor.init(e.data);
          eventBus.$emit('refreshMap',{editor:this.editor});
       }catch(e){
         window.console.log(e);
         alert('不支持的数据');
       }
     });

     eventBus.$on('copy',()=>{
        if(!this.editStatus){
            if(this.status=='selectNode'||this.status=='selectNodes'){
               this.editor.copy('node',this.editor.getSelectNodes());
            }else if(this.status=='selectGroup'){
               this.editor.copy('group',this.group);
            }
        }
     });


     eventBus.$on('mirror',()=>{
        if(!this.editStatus){
            if(this.status=='selectNode'||this.status=='selectNodes'){
               this.editor.mirror('node',this.node);
               //eventBus.$emit('clearSelect');  
            }
        }
     });

     eventBus.$on('delete',()=>{
        if(this.status=='selectNode'){
           this.editor.execute('deleteNode',{node:this.node});
        }else if(this.status=='selectNodes'){
           this.editor.execute('deleteNodes',{nodes:this.nodes});
        }else if(this.status=='selectGroup'){
           this.editor.execute('deleteGroup',{group:this.group});
        }else if(this.status=='selectEd ge'){
           this.editor.execute('deleteEdge',{edge:this.edge});
        }
        if(this.editor.isMultiSelectMode) {
          this.editor.execute('deleteNodes',{nodes:this.editor.selectNodes});
          this.editor.resetMultiSelect();
        }
     });

    eventBus.$on('deleteAll',()=>{
      this.editor.execute('deleteAll',{node:this.node});
    });
    eventBus.$on('saveData',()=>{
      this.exportDesign();
      that.dowloadDetail.isShow = true;
    });    
         
      eventBus.$on('changeNode',(e)=>{
           var selectNodes=e.nodes||this.editor.getSelectNodes();
           this.editor.execute('changeNode',e,selectNodes);
      });

      eventBus.$on('align',(e)=>{
         this.editor[e.align]&&this.editor[e.align]();
      });

      eventBus.$on('changeGroup',(e)=>{
         this.editor.execute('changeGroup',e);
      });

      eventBus.$on('changeEdge',(e)=>{
         this.editor.execute('changeEdge',e);
      });

      eventBus.$on('ungroup',()=>{
        if(!this.editStatus){
           if(this.status=='selectGroup'){
             this.editor.execute('ungroup',{group:this.group});
           }
        }
     });

     eventBus.$on('selectFrame',()=>{
        this.showMultiSelectPanel = true;
        this.editor.emit('selectFrame');
     });

     eventBus.$on('shadow',(e)=>{
         var shadow=e.shadow;
         if(shadow){
           this.editor.setShadow();
         }else{
           this.editor.cancelShadow();
         }
     });
      

     this.editor.listen('selectNode',(e)=>{
       that.status='selectNode';
          that.node=e.node;
          eventBus.$emit('selectNode',{node:e.node});
          if((e.node.kind === 2 || e.node.kind === 3) && !that.editor.isMultiSelectMode) {
            that.editor.checkObstaceDistance(e.node);
          }
     });

     this.editor.listen('selectNodes',(e)=>{
            if(e&&e.nodes.length>1){
                that.status='selectNodes';
                that.nodes=e.nodes;
                eventBus.$emit('selectNodes',{nodes:e.nodes});
            }else{
                var selectNodes=this.editor.getSelectNodes();
                if(selectNodes.length==1){
                    that.status='selectNode';
                    that.node=selectNodes[0];
                    eventBus.$emit('selectNode',{node:selectNodes[0]});
                }else if(selectNodes.length>1){
                    that.status='selectNodes';
                    that.nodes=selectNodes;
                    eventBus.$emit('selectNodes',{nodes:selectNodes});
                }
            }
     });

     this.editor.listen('selectGroup',(e)=>{
          that.status='selectGroup';
          that.group=e.group;
          eventBus.$emit('selectGroup',e);
     });

     this.editor.listen('selectEdge',(e)=>{
        that.status='selectEdge';
        this.editor.status='selectEdge';
        that.edge=e.edge;
        eventBus.$emit('selectEdge',e);
     });


     this.editor.listen('undoredo',(e)=>{
       eventBus.$emit('undoredo',e);
       eventBus.$emit('refreshMap',{editor:this.editor});
     });

     this.editor.listen('clearSelectItems',()=>{
        eventBus.$emit('clearSelect');
        this.editor.execute('clearOBAlert', this.node);
     });

     function keepLastIndex(obj) {
        if (window.getSelection) {//ie11 10 9 ff safari
            obj.focus(); //解决ff不获取焦点无法定位问题
            let range = window.getSelection();//创建range
            range.selectAllChildren(obj);//range 选择obj下所有子内容
            range.collapseToEnd();//光标移至最后
        }
        else if (document.selection) {//ie10 9 8 7 6 5
            let range = document.selection.createRange();//创建选择对象
            //var range = document.body.createTextRange();
            range.moveToElementText(obj);//range定位到obj
            range.collapse(false);//光标移至最后
            range.select();
        }
    }

     this.editor.listen('edit',(e)=>{
        that.editStatus=e.status;
        var g=new zrender.Group();
        var box=null,text=e.text,box1=null;
        that.editModel=true;
       
        switch(e.status){
           case 'editNode':
            that.node=e.node;
            box=g.getBoundingRect([this.node]);
            that.left=box.x+this.editor.position[0];
            that.top=box.y+this.editor.position[1];
            that.width=box.width;
            that.height=box.height;
            that.text=that.domText(text);
            text='';
            break;
           case 'editGroup':
            that.editGroup=e.group;
            box=g.getBoundingRect([that.group.groupHead]);
            box1=g.getBoundingRect([that.group]);
            that.left=box1.x+this.editor.position[0];
            that.top=box1.y+this.editor.position[1];
            that.width=box.width;
            that.height=box.height;
           // text=that.group.groupHead.style.text;
            that.text=that.domText(text);
            text='';
            break;
           case 'editEdge':
             that.edge=e.edge;
             var d=e.edge.getData();
            if(d.text){
                box=g.getBoundingRect([e.edge.relateText]);
                that.left=box.x+this.editor.position[0];
                that.top=box.y+this.editor.position[1];
                that.width=box.width;
                that.height=box.height;
                that.text=that.domText(d.text);
             }else{
                var cpx=d.cpx;
                that.left=cpx.x1+this.editor.position[0];
                that.top=cpx.y1+this.editor.position[1];
                that.width=80;
                that.height=20;
             }
            break;
           
        }

        that.$nextTick(function () {
           //that.$refs['edit'].$el.focus();
           keepLastIndex( that.$refs['edit'].$el);
        });
     });
  },
  methods:{
    resetMultiSelectMode() {
      this.editor.resetMultiSelect();
    },
    exitMultiSelectMode() {
      this.showMultiSelectPanel = false;
      this.editor.quitMultiSelect();
      eventBus.$emit('isInSelectMode',{mode:false});
    },

    calSafe(nodes) {
      var safe = {
        left : 0,
        right : 0,
        up : 0,
        down : 0,
        back : 0,
        operation : 0
      };
      var maxUp = 0;
      var maxDown = 0;
      safe.left = nodes[0].safe.left;
      safe.right = nodes[nodes.length-1].safe.right;
      for(let i=0; i<nodes.length; i++) {
        maxUp = Math.max(maxUp, nodes[i].safe.up)
        maxDown = Math.max(maxDown, nodes[i].safe.down)
      }
      safe.up = maxUp
      safe.down = maxDown
      return safe;
    },
    
    buildCombine () {
      var nodes = this.editor.selectNodes;
      if(nodes.length == 0) return;
      var config = {
          safe: this.calSafe(nodes),
          name: '',
          alignType : this.alignType,
          face : this.alignType === '0' ? '2' : '0'
        }
      var rebuildNodes = [];
      nodes.forEach(node => {
        if(node.command === 'combine') {
          node.data.originData.nodes.forEach(n => {
            rebuildNodes.push(n);
          })
        } else {
          rebuildNodes.push(node)
        }
      })
      var node = this.buildCombineNode(rebuildNodes, config);
      var combine = this.combineCommandHelper(node, this.editor.scale);
      combine.x = nodes[0].position[0];
      combine.y = nodes[0].position[1];
      combine.draggable = false;
      this.editor.resetMultiSelectAndDeleteOld();
      this.editor.execute('addShape', combine);
    },

     dragover(e){
       e.preventDefault();
     },
     dragstart(e){
       e.preventDefault();
     },
     drop(e){
        if(document.getElementById('combine').style.display != 'none') {
          return;
        }
        var text= e.dataTransfer.getData("addShape");
        var x,y;
        if(e.offsetX) {
          x=parseInt(e.offsetX)+0.5;
          y=parseInt(e.offsetY)+0.5;
        } else {
          x=parseInt(e.pageX)+0.5 - 140;
          y=parseInt(e.pageY)+0.5 - 50;
        }

        try{
          var data=JSON.parse(text);
          data['x']=x;
          data['y']=y;
          this.editor.addShape(data);
        }catch(err){
           //window.console.log(err);
        }
     },
     trim(x) {
        return x.replace(/^\s+|\s+$/gm,'');
     },

     exportDesign() {
       this.dowloadDetail.isShow = true;
       var res = []
       var facilities = this.editor.facilities;
       var summary = 0;
       for(let i=0; i<facilities.length; i++) {
         let has = false;
         for(let j=0; j<res.length; j++) {
           if(facilities[i].originData.name === res[j].name) {
             res[j].count++;
             res[j].cost += facilities[i].originData.cost;
             summary += facilities[i].originData.cost;
             has = true;
           }
         }
         if(!has) {
          res.push({
            name : facilities[i].originData.name,
            count : 1,
            cost : facilities[i].originData.cost
          })
          summary += facilities[i].originData.cost;
         }
       }
       res.push({
          name:'总计',
          count : "",
          cost : summary
       })
       this.datas = res;
       this.summary = summary;
     },

    removeDowloadMsg() {
      var downLoadNodes = this.editor.downLoadText;
      if(downLoadNodes && downLoadNodes.length>0) {
        this.editor.execute('deleteNodes',{nodes:downLoadNodes});
      }
    },

    AddText (config) {
      var textCmd = {
        style: {
            text:  config.name,
            fontSize: config.size,
            fontWeight: config.isBold ? 'bold' : 'normal'
        },
        command:'text',
        position:[0,0],
        draggable: false,
        x:config.x,
        y:config.y,
        kind:6
      }
      this.editor.execute('addShape', textCmd);
    },
    buildDownloadMsg (){
      this.removeDowloadMsg();
      var startY = 30;
      //设计图
      this.AddText({
          name : '设计图:' ,
          size : 20,
          isBold : false,
          x : this.offsetX + 5,
          y : 10
      })
      //标题
      this.AddText({
        name : this.dowloadDetail.name,
        size : 40,
        isBold : true,
        x : this.offsetX + 900,
        y : startY
      })
      startY += 40;
      //设备使用信息
      for(let i=0; i<this.datas.length; i++) {
        startY += 30;
        this.AddText({
          name : this.datas[i].name ,
          size : 20,
          isBold : false,
          x : this.offsetX + 900,
          y : startY
        })
        this.AddText({
          name : this.datas[i].count + '个',
          size : 20,
          isBold : false,
          x : this.offsetX + 1100,
          y : startY
        })
        this.AddText({
          name : this.datas[i].cost + '元',
          size : 20,
          isBold : false,
          x : this.offsetX + 1250,
          y : startY
        })
      }
      startY += 40;
      //总计使用信息
        this.AddText({
          name : "总计： " + this.summary + "元",
          size : 30,
          isBold : true,
          x : this.offsetX + 900,
          y : startY
        })
    },
     download(type){
       //var me = this;
       if(this.dowloadDetail.name === '') return;
          var box=null;
         //var editor=null;
         switch(type){
           case 'json':
             var str=JSON.stringify(this.editor._());
             this.downloadFile(str,'flowchart.json');
             break;
           case 'png':
                this.showDesign = true;
                this.editor.clearFaAlert();
                this.editor.clearOBAlert();
                this.editor.clearSelectItems();
                var flowEdtor = document.getElementById("flowEditor");
                flowEdtor.style.border = '2px solid';
                //this.buildDownloadMsg();
                  // box = {
                  //   x:this.offsetX,
                  //   y:this.offsetY,
                  //   width:600,
                  //   height:800
                  // }

                // document.getElementById('download-canvas').style.width=box.width+'px';
                // document.getElementById('download-canvas').style.height=box.height+'px';

                // editor=new flowEditor('flowEditor-copy');
                // editor.init(JSON.parse(JSON.stringify(this.editor.getData())),true);


                // editor.attr({
                //   position:[-parseInt(box.x),-parseInt(box.y)]
                // });
                // this.editor.position[0] = -parseInt(box.x);
                // this.editor.position[1] = -parseInt(box.y);
                // editor.position[0] = [-parseInt(box.x)];
                //  editor.position[1] = [-parseInt(box.y)];
              //  try{
              //     this.editor.zr.painter.getRenderedCanvas({
              //        backgroundColor:"white"
              //     }).toBlob((blob) => {
              //       var url = window.URL.createObjectURL(blob);
              //       window.console.log(url);
              //       window.open(url);
              //     }, 'image/png');
              //  }catch(e){
              //      document.getElementById('download-win').style.display='block';
              //  }
              // box=this.editor.getBoundingRect(this.editor.nodes.concat(this.editor.groups).concat(this.editor.edges));
                box = {
                  x:this.offsetX,
                  y:this.offsetY,
                  width:3000,
                  height:2000
                }

                // document.getElementById('download-canvas').style.width=box.width+'px';
                // document.getElementById('download-canvas').style.height=box.height+'px';
                // editor=new flowEditor('download-canvas');
                // // editor.attr({
                // //   position:[-300,-300]
                // // });
                // editor.init(JSON.parse(JSON.stringify(this.editor.getData())),true);
                // editor.attr({
                //   position:[-10,-10]
                // });
                //setTimeout(function(){ 

                //范围截取cavans
                // var canvas = document.getElementById('flowEditor').children[0].children[0];
                // var ctx= canvas.getContext("2d");
                // var imgData= ctx.getImageData(box.x,box.y,box.width,box.height);
                // var downloadCanvas = document.createElement("canvas");
                // downloadCanvas.width=box.width;
                // downloadCanvas.height=box.height;
                // var oCtx = downloadCanvas.getContext("2d");
                //   // for (var i = 0; i < imgData.data.length; i += 4) {
                //   //     // 当该像素是透明的，则设置成白色
                //   //     if (imgData.data[i + 3] == 0) {
                //   //         imgData.data[i] = 255;
                //   //         imgData.data[i + 1] = 255;
                //   //         imgData.data[i + 2] = 255;
                //   //         imgData.data[i + 3] = 255;
                //   //     }
                //   // }
                //   oCtx.putImageData(imgData, 0, 0);
                //   downloadCanvas.toBlob((blob) => {
                //     var url = window.URL.createObjectURL(blob);
                //     document.getElementById("designImg").src = url;  // 将生成的图片添加到body
                //   }, 'image/png');

                  //window.open(oGrayImg.src);
                  // editor.zr.painter.getRenderedCanvas({
                  //    backgroundColor:"white"
                  // }).toBlob((blob) => {
                  //   var url = window.URL.createObjectURL(blob);
                  //   window.console.log(url);
                  //   window.open(url);
                  // }, 'image/png');

                    window.pageYOffset = 0;
                    document.documentElement.scrollTop = 0
                    document.body.scrollTop = 0

                    var canvas2 = document.createElement("canvas");
                    var design = document.getElementById("editor");
                    var w = parseInt(window.getComputedStyle(design).width);
                    var h = parseInt(window.getComputedStyle(design).height);
                    //将canvas画布放大若干倍，然后盛放在较小的容器内，就显得不模糊了
                    var scale = 3;
                    canvas2.width = w * scale;
                    canvas2.height = h * scale;
                    canvas2.style.width = w + "px";
                    canvas2.style.height = h + "px";
                    //可以按照自己的需求，对context的参数修改,translate指的是偏移量   
                    var context = canvas2.getContext("2d");
                    context.scale(scale,scale);

                    // design.html2canvas({background:'#fff', onrendered: function( canvas ) {  
                    //     canvas.toBlob((blob) => {
                    //     var url = window.URL.createObjectURL(blob);
                    //     window.console.log(url);
                    //     window.open(url);s
                    //   }, 'image/png'); 
                    // }});

                    html2canvas(design,{
                          canvas: canvas2,
                          allowTaint: true,
                          useCORS: true,
                          background:'white',
                          scale: 1, //设置放大的倍数
                          // height : design.scrollHeight,
                          // windowHeight: design.scrollHeight
                          scrollY: -window.scrollY
                        }).then(canvas => {
                        const img = new Image()
                        img.src =  canvas.toDataURL("image/png" ,1.0);
                        img.style.width = "1200px"
                        const newWin = window.open('/export.html', '_blank')
                        newWin.document.write(img.outerHTML)
                        newWin.document.title = 'wjmc'
                        newWin.document.close()
                    });
                    //}, 1);

                    // domtoimage.toPng(design)
                    //   .then(function (dataUrl) {
                    //     const img = new Image()
