import zrender from 'zrender'
import * as roundRectHelper  from '../help/roundRect'
import mixin from '../help/mixin'
import common from './common'
var combine = zrender.Path.extend({
    type: 'combine',
    shape: {
        x: 0,
        y: 0,
        width: 0,
        height: 0
    },
    kind : 3,
    rendered: false,
    alerts: [],
    //default
    safe: {
        up: 0,
        right: 150,
        down: 0,
        left: 100
    },
    face: "1",
    alignFace : "0",
    buildPath: function (ctx, shape) {
        var x=shape.x;
        var y=shape.y;
        var dx = x;
        var dy = y;
        var r = 2;
        shape.r=r;
        var nodes = shape.nodes;
        var addOperationLine = function (ctx, node, x, y) {
            if(this.face === "0") {
                let y0=parseInt(y + node.height/10);
                ctx.moveTo(x,y0);
                ctx.lineTo(x + node.width,y0);        
            } else if(this.face === "1") {
                let x2=parseInt(x + node.width/10*9);
                ctx.moveTo(x2,y);
                ctx.lineTo(x2, y + node.height);
            } else if(this.face === "2") {
                var y4=parseInt(y + node.height/10*9);
                ctx.moveTo(x,y4);
                ctx.lineTo(x + node.width,y4);  
            } else {
                var x1=parseInt(x + node.width/10);
                ctx.moveTo(x1,y);
                ctx.lineTo(x1, y + node.height);
            }
        };
        for(let i=0; i<nodes.length; i++) {
            if(this.alignFace === '0'){
                let s = {
                    width : nodes[i].width,
                    height : nodes[i].height,
                    x : dx,
                    y : dy
                }
                roundRectHelper.buildPath(ctx,s);
                addOperationLine.call(this, ctx, nodes[i], dx, dy);
                dx += s.width;
            } else if(this.alignFace === '3'){
                let s = {
                    width : nodes[i].width,
                    height : nodes[i].height,
                    x : dx,
                    y : dy
                }
                roundRectHelper.buildPath(ctx,s);
                //this.drawRectText(originCtx, s);
                addOperationLine.call(this, ctx, nodes[i], dx, dy);
                dy += s.height;
            } else if(this.alignFace === '1') {
                let s = {
                    width : nodes[i].width,
                    height : nodes[i].height,
                    x : nodes[i].width < shape.width ? (dx + shape.width - nodes[i].width) : dx,
                    y : dy
                }
                roundRectHelper.buildPath(ctx,s);
                //this.drawRectText(originCtx, s);
                addOperationLine.call(this, ctx, nodes[i], s.x, dy);
                dy += s.height;
            } else {
                let s = {
                    width : nodes[i].width,
                    height : nodes[i].height,
                    x : dx,
                    y : nodes[i].height < shape.height ? (dy + shape.height - nodes[i].height) : dy,
                }
                roundRectHelper.buildPath(ctx,s);
                //this.drawRectText(originCtx, s);
                addOperationLine.call(this, ctx, nodes[i], dx, s.y);
                dx += s.width;
            }
        }

        ctx.closePath();
        //this.customText(this.data.ctx);
        return ;
    },

    customText : function (ctx) {
        var shape = this.shape;
        var x=shape.x;
        var y=shape.y;
        var dx = x;
        var dy = y;
        var nodes = shape.nodes;
        for(let i=0; i<nodes.length; i++) {
            let name = nodes[i].name;
            this.style.text = name;
            if(this.alignFace === '0'){
                let s = {
                    width : nodes[i].width,
                    height : nodes[i].height,
                    x : dx,
                    y : dy,
                }
                this.drawRectText(ctx, s);
                dx += s.width;
            } else if(this.alignFace === '3'){
                let s = {
                    width : nodes[i].width,
                    height : nodes[i].height,
                    x : dx,
                    y : dy,
                }
                this.drawRectText(ctx, s);
                dy += s.height;
            } else if(this.alignFace === '1') {
                let s = {
                    width : nodes[i].width,
                    height : nodes[i].height,
                    x : nodes[i].width < shape.width ? (dx + shape.width - nodes[i].width) : dx,
                    y : dy, 
                }
                this.drawRectText(ctx, s);
                dy += s.height;
            } else {
                let s = {
                    width : nodes[i].width,
                    height : nodes[i].height,
                    x : dx,
                    y : nodes[i].height < shape.height ? (dy + shape.height - nodes[i].height) : dy,
                }
                this.drawRectText(ctx, s);
                dx += s.width;
            }
        }
        this.style.text = ''
    }
});

class Combine extends combine {
    constructor(data) {
        super(data);
        this.data = data;
        this.oldfill = this.data.style.fill;
        this.anchors = [];
        this.nodeType = "node";
        this.createAnchors();
    }
 
    createAnchors() {
    }
  
}

mixin(common,Combine.prototype);

export default Combine;