<template>
  <div class="header">
      <span style="float:left;margin-left:20px;font-size:16px;font-weight:bold;">舟山启明电力设计院模拟设计器</span>
        <span @click.stop="openFile" class="iconfont icon-open" style="float:left;margin-left:10px;font-size:18px;display:none"></span>
        <ul>
            <li>
              <Button class = "head-button" @click="modal1 = true">清除</Button>
              <Modal
                v-model="modal1"
                title="清空"
                @on-ok="command(delAllIcon)"
                @on-cancel="cancel">
                <p>确定要清空画布上所有内容吗？</p>
            </Modal>
            </li>
            <li v-for="(item ,index) in icon" v-bind:key="index">
              <Button  @click="command(item)" v-html="item.name" v-bind:class="'head-button '+ item.class" :disabled="item.disable"></Button>
            </li>

            <!-- <li @click="command(item)" v-for="(item ,index) in icon" v-bind:key="index" :title="item.cmd" v-bind:class="'iconfont '+item.icon+' '+item.class"></li> -->
            <li>
                <Button class ="head-button" @click.stop="save('png')">下载设计图</Button>
            </li>
        </ul>
        <input type="file" id="file" style="display:none"/>
        
        <!-- <div class="language" @mouseenter="mouseenter" @mouseleave="mouseleave">
              <span>{{lang}}</span>
              <ul v-show="showLang">
                  <li @click.stop="changeLanguage('zh')">中文</li>
                  <li @click.stop="changeLanguage('en')">English</li>
             </ul>
        </div> -->
        
        
  </div>
</template>

<script>
import eventBus from '../eventbus';
export default {
  name: 'Header',
  data(){
     return{
        language:'zh',
        showLang:false,
        isInSelectMode:false,
        delAllIcon: {
          icon:'icon-shanchu',
          name:'清除',
          cmd:'deleteAll',
          class:'enable'
        },
        icon:[
              //  {
              //   icon:'icon-undo',
              //   name:'撤销',
              //   cmd:'undo',
              //   class:'disable'
              // },
              // {
              //  icon:'icon-redo',
              //  name:'恢复',
              //  cmd:'redo',
              //  class:'disable'
              // }, 
            //   {
            //    icon:'icon-shanchu',
            //    name:'清除',
            //    cmd:'deleteAll',
            //    class:'enable'
            //  },
              {
                icon:'icon-copy',
               name:'复制',
               cmd:'copy',
               disable: true,
               class:'disable'
            },
              {
              icon:'icon-shanchu',
              name:'删除',
              cmd:'delete',
              disable: true,
              class:'disable'
            },
              {
               icon:'icon-redo',
               name:'旋转',
               cmd:'rotate',
               disable: true,
               class:'disable'
              },
              {
               icon:'icon-redo',
               name:'镜像',
               cmd:'mirror',
               disable: true,
               class:'disable'
              },
              {
               icon:'icon-redo',
               name:'信息',
               cmd:'showInfoDraw',
               disable: true,
               class:'enable'
              },
            // {
            //    icon:'icon-fengexian',
            //    name:'分割',
            //    cmd:'line',
            //    class:'disable'
            //  },
            //  {
            //    icon:'icon-to-front',
            //    name:'最上层',
            //    cmd:'tofront',
            //    class:'disable'
            //  },
            //  {
            //    icon:'icon-to-back',
            //    name:'最下层',
            //    cmd:'toback',
            //    class:'disable'
            //  },
            //  {
            //    icon:'icon-fengexian',
            //    name:'分割',
            //    cmd:'line',
            //    class:'disable'
            //  },
              {
               icon:'icon-kuangxuan',
               name:'多选',
               cmd:'selectFrame',
               class:'enable',
             },
            //  {
            //    icon:'icon-group',
            //    name:'成组',
            //    cmd:'createGroup',
            //    class:'disable'
            //  },
            //   {
            //    icon:'icon-ungroup',
            //    name:'取消成组',
            //    cmd:'ungroup',
            //    class:'disable'
            //  }
        ],
        disable:true,
        upload:false,
        modal1:false
        
     }
  },
  computed:{
    lang(){
         return this.language=='zh'?'中文':'English';
    }
  },
  mounted(){


    eventBus.$on('isInSelectMode',(e)=>{
      this.isInSelectMode = e.mode;
    });

    eventBus.$on('selectNode',()=>{
      this.status="selectNode";
      if(!this.isInSelectMode) {
        this.changeDisable(true);
      }
    });

    eventBus.$on('clearSelect',()=>{
      this.status="clearSelect";
       if(!this.isInSelectMode) {
        this.changeDisable(false);
       }
    });

    eventBus.$on('selectGroup',()=>{
      this.status="selectGroup";
      this.changeDisable(true);
    });

    eventBus.$on('selectEdge',()=>{
       this.status="selectEdge";
       this.changeDisable(true);
    });

    var fileDom=document.getElementById('file');
   // fileDom.removeEventListener('change');
    fileDom.addEventListener('change',(e)=>{
         var file=e.target.files[0];
        if(file&&file.name.endsWith('json')){
          var reader = new FileReader();//这里是核心！！！读取操作就是由它完成的。
          reader.readAsText(file);//读取文件的内容
          reader.onload = function(){
            try{
              eventBus.$emit('initFlow',{data:JSON.parse(this.result)});
            }catch(e){
              alert('数据出错')
            }
          }
        }
        fileDom.value='';
    },false);

    this.language=localStorage.getItem('localeLanguage')||'zh';
    
    this.changeLanguage(this.language);

  },
  methods:{

    openFile(){
       document.getElementById('file').click();
    },

    changeLanguage(lang){
         this.language=lang;
         eventBus.$emit('changeLanguage',{
           language:lang
         });
    },
    mouseenter(){
         this.showLang = true;
    },
    mouseleave(){
         this.showLang = false;
    },
    command(item){
      if(!item.disable){
        eventBus.$emit(item.cmd);
        if(item.cmd === 'selectFrame') {
          this.changeDisable(false);
          this.icon[1].disable = false;
          this.isInSelectMode = true;
        }
      }
    },
    changeDisable(flag){
      if(flag){
        this.icon.forEach(item=>{
            if(['undo','redo','deleteAll', 'selectFrame'].indexOf(item.cmd)==-1){
                item.disable = false;
                item.class='';
            }
        });
      }else{
        this.icon.forEach(item=>{
              if(['undo','redo','deleteAll', 'selectFrame'].indexOf(item.cmd)==-1){
                item.disable = true;
                item.class='disable';
            }
         });
      }
    },
    showUpload(){
         this.upload=!this.upload;
    },
    save(type){
       this.upload=false;
       eventBus.$emit('saveData',{type});
    }
  }
}
</script>

<style scoped>
.head-button{
  height: 25px;
  padding : 0 10px
}
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
  margin-top:0;
  margin-bottom: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
  cursor: pointer;
}
a {
  color: #42b983;
}
.header{
   -webkit-user-select: none;
   user-select: none;
   line-height: 40px;
   height: 40px;
   border:1px solid #e6e9ed;
   box-sizing: border-box;
   -webkit-box-sizing: border-box;
   text-align: center;
}
ul li.disable{
    color:#ccc;
}
li ul{
  position: absolute;
  border:1px solid #f5f5f5;
  cursor: pointer;
  z-index: 200;
}
li ul li{
  display: block;
  text-align: left;
  font-size: 16px;
  line-height: 30px;
  background: #fff;
  padding:0 10px;
  margin:0;
}
li ul li span{
  font-size: 12px;
}
li ul li:hover{
  background: #f5f5f5;
}
.language{
  position: absolute;
  right: 10px;
  top:-2px;
  font-size: 12px;
  z-index: 20;
  background: #fff;
  width:60px;
}
.language ul{
  border:1px solid #e5e5e5;
}
.language li {
  display: block;
}
</style>
