import zrender from 'zrender'
import * as roundRectHelper  from '../help/roundRect'
import mixin from '../help/mixin'
import common from './common'
var preRect = zrender.Path.extend({
    type: 'preRect',
    shape: {
        x: 0,
        y: 0,
        width: 0,
        height: 0
    },
    kind : 3,
    alerts: [],
    //default
    safe: {
        up: 0,
        right: 150,
        down: 0,
        left: 100
    },
    face: "1",
    buildPath: function (ctx, shape) {
        var x=shape.x;
        var y=shape.y;
        var width = shape.width;
        var height = shape.height;
        var r = 2;
        shape.r=r;
        roundRectHelper.buildPath(ctx,shape);
        if(this.face === "2") {
            var y4=parseInt(y+height/10*9);
            ctx.moveTo(x,y4);
            ctx.lineTo(x+width,y4);            
        } else if(this.face === "1") {
            var x2=parseInt(x+width/10*9);
            ctx.moveTo(x2,y);
            ctx.lineTo(x2,y+height);
        } else if(this.face === "0") {
            var y3=parseInt(y+height/10);
            ctx.moveTo(x,y3);
            ctx.lineTo(x+width,y3);
        } else {
            var x1=parseInt(x+width/10);
            ctx.moveTo(x1,y);
            ctx.lineTo(x1,y+height);
        }
        ctx.closePath();
        return ;
    }
});

class PreRect extends preRect {
    constructor(data) {
        super(data);
        this.data = data;
        this.oldfill = this.data.style.fill;
        this.anchors = [];
        this.nodeType = "node";
        this.createAnchors();
    }
 
    createAnchors() {
    }
  
}

mixin(common,PreRect.prototype);

export default PreRect;